import { ArtifactSummary, ArtifactSummaryType } from "./ArtifactSummary"
import { AssetsCollection } from "./Assets"
import { DetailedResult } from "./DetailedResult"
import type { HighlightedResult } from "./HighlightedResult"
import { MemoryDumpSummary } from "./MemoryDumpSummary"
import { SyncOverAsyncHighlightedResult } from "./SyncOverAsyncHighlightedResult"
import { TraceSummary } from "./TraceSummary"

/**
 * Represents a raw result.
 */
export interface RawResult {
    /**
     * The error code.
     */
    ErrorCode: string;
    /**
     * The analyzer id.
     */
    AnalyzerId: string;
    /**
     * The analysis id.
     */
    AnalysisId: string;
}

interface CLIJson {
    interpretedResults: unknown[];
    results: RawResult[];
    assets: unknown;
    cliSummary: unknown;
}

/**
 * Represents an analysis result.
 */
export class AnalysisResult {
    #_rawResult: RawResult | null;
    #_detailedResult: DetailedResult;

    /**
     * @param {RawResult} rawResult The raw result.
     * @param {DetailedResult} detailedResult The detailed result.
     */
    constructor(rawResult: RawResult | null, detailedResult: DetailedResult) {
        this.#_rawResult = rawResult
        this.#_detailedResult = detailedResult
    }

    /**
     * The raw result.
     *
     * @returns {RawResult | null} The raw result or null if it's missing.
     */
    public get rawResult(): RawResult | null {
        return this.#_rawResult
    }

    /**
     * The localized detailed result.
     *
     * @returns {DetailedResult} The detailed result.
     */
    public get detailedResult(): DetailedResult {
        return this.#_detailedResult
    }
}

/**
 * Represents an analysis containing results and additional information about the target.
 */
export class Analysis {
    /**
     * Parse an analysis from raw input.
     *
     * @param {any} jsonObject The raw input (schema available here: https://dev.azure.com/devdiv/DevDiv/_wiki/wikis/DevDiv.wiki/23255/Detailed-Analysis-results?anchor=json-schema)
     * @returns {Analysis} The analysis.
     */
    public static parse(jsonObject: unknown): Analysis {
        const input = jsonObject as CLIJson

        if (!input || !input.interpretedResults) {
            throw new Error("Malformed input! The 'interpretedResults' property is missing.")
        }
        const rawResults = input.results
        const analysisResults: AnalysisResult[] = []

        let artifactSummary: ArtifactSummary | null = null
        const syncOverAsyncResult = new SyncOverAsyncHighlightedResult()

        input.interpretedResults.forEach((r) => {
            const detailedResult = new DetailedResult(r)
            let matchingRawResult: RawResult | null = null

            if (rawResults) {
                for (let idx = 0; idx < rawResults.length; idx++) {
                    if (rawResults[idx].ErrorCode === detailedResult.errorCode) {
                        matchingRawResult = rawResults[idx]

                        const artifactSummaryType: ArtifactSummaryType = ArtifactSummary.getArtifactSummaryType(matchingRawResult)
                        if (artifactSummaryType !== ArtifactSummaryType.None) {
                            // We currently assume there is only one artifact summary and will drop any subsequent summaries found.
                            if (artifactSummary !== null) {
                                return // continue
                            } else if (artifactSummaryType === ArtifactSummaryType.MemoryDump) {
                                artifactSummary = new MemoryDumpSummary(matchingRawResult)
                            } else {
                                artifactSummary = new TraceSummary(matchingRawResult)
                            }

                            return // continue
                        }

                        break
                    }
                }
            }

            if (SyncOverAsyncHighlightedResult.isSyncOverAsyncResult(detailedResult)) {
                if (syncOverAsyncResult.tryAddResult(new AnalysisResult(matchingRawResult, detailedResult))) {
                    return // continue
                }
            }

            analysisResults.push(new AnalysisResult(matchingRawResult, detailedResult))
        })

        // in the future, when multiple highlighted results might be found we need to pick one and re-add the other back into analysisResults.
        const highlightedResult = syncOverAsyncResult.isValid ? syncOverAsyncResult : null

        analysisResults.sort((a: AnalysisResult, b: AnalysisResult) => {
            return b.detailedResult.sortWeight - a.detailedResult.sortWeight
        })
        let analyzerVersion: string | null = null
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        if (input.cliSummary && "version" in (input.cliSummary as any)) {
            // eslint-disable-next-line @typescript-eslint/no-unsafe-member-access, @typescript-eslint/no-explicit-any
            analyzerVersion = (input.cliSummary as any).version as string
        }
        return new Analysis(analysisResults, highlightedResult, artifactSummary, new AssetsCollection(input.assets), analyzerVersion)
    }

    /**
     * The analyis results.
     *
     * @returns {DetailedResult[]} Array of AnalysisResults.
     */
    public get results(): AnalysisResult[] {
        return this.#_results
    }

    /**
     * The analyis assets.
     *
     * @returns {AssetsCollection} The assets object.
     */
    public get assets(): AssetsCollection {
        return this.#_assets
    }

    /**
     * The analyis highlighted result.
     *
     * @returns {HighlightedResult | null} The highlighted result.
     */
    public get highlightedResult(): HighlightedResult | null {
        return this.#_highlightedResult
    }

    /**
     * The artifact summary.
     *
     * @returns {ArtifactSummary | null} The summary.
     */
    public get summary(): ArtifactSummary | null {
        return this.#_summary
    }

    /**
     * The version of the tool that produced the analysis
     *
     * @returns {string | null} The version, if available.
     */
    public get analyzerVersion(): string | null {
        return this.#_analyzerVersion
    }

    /**
     * ctor
     *
     * @param {AnalysisResult[]} results The results.
     * @param {HighlightedResult | null} highlightedResult The highlighted result.
     * @param {ArtifactSummary | null} summary The summary of the artifact being analyzed.
     * @param {AssetsCollection} assets the assets that have been parsed.
     * @param {string | null} analyzerVersion the analyzer version.
     */
    private constructor(results: AnalysisResult[], highlightedResult: HighlightedResult | null, summary: ArtifactSummary | null, assets: AssetsCollection, analyzerVersion: string | null) {
        this.#_results = results
        this.#_highlightedResult = highlightedResult
        this.#_summary = summary
        this.#_assets = assets
        this.#_analyzerVersion = analyzerVersion
    }

    #_results: AnalysisResult[];
    #_highlightedResult: HighlightedResult | null;
    #_summary: ArtifactSummary | null;
    #_assets: AssetsCollection;
    #_analyzerVersion: string | null;
}