import { getTheme, IButtonStyles, IconButton, IModalStyles, Modal, DefaultButton } from "@fluentui/react";
import React from "react";
import { RenderUtils } from "../../RenderUtils";
import "./Navigator.less";

export class Navigator extends React.Component<unknown, { modalContent: React.ReactNode }> {
  private static _navigatorSingleton: Navigator = null as unknown as Navigator;
  private static _routes: Map<string, (slug: string) => React.ReactNode> = new Map();
  private static _errorCodeToSlugs: Map<string, Array<string>> = new Map();

  constructor(props: any) {
    super(props);

    if (Navigator._navigatorSingleton) {
      throw "The navigator singleton was already constructed!";
    }
    Navigator._navigatorSingleton = this;

    this.state = { modalContent: null };
  }

  public static registerRoute(slug: string, errorCode: string, renderFunc: (slug: string) => React.ReactNode) {
    if (Navigator._routes.has(slug)) {
      throw "Route " + slug + " is already registered!";
    }
    Navigator._routes.set(slug, renderFunc);
    if (Navigator._errorCodeToSlugs.has(errorCode)) {
      Navigator._errorCodeToSlugs.get(errorCode)?.push(slug);
    } else {
      Navigator._errorCodeToSlugs.set(errorCode, [slug]);
    }

    const curSlug = RenderUtils.GetCurrentSlug();
    if (curSlug === slug) {
      Navigator._navigatorSingleton.setState({
        modalContent: renderFunc(curSlug),
      });
    }
  }

  public static showModal(slug: string) {
    if (!Navigator._navigatorSingleton) {
      return;
    }

    const renderFunc = Navigator._routes.get(slug);
    if (renderFunc) {
      Navigator._navigatorSingleton.showModalInternal(slug, renderFunc(slug));
    }
  }

  public static showFirstModalForErrorCode(errorCode: string) {
    if (!Navigator._navigatorSingleton) {
      return;
    }

    const slugs = Navigator._errorCodeToSlugs.get(errorCode);
    if (!slugs || slugs.length == 0) {
      return;
    }

    Navigator.showModal(slugs[0]);
  }

  public showModalInternal(slug: string, content: React.ReactNode) {
    RenderUtils.UpdateCurrentSlug(slug);
    this.setState({ modalContent: content });
  }

  private hideModal = () => {
    RenderUtils.UpdateCurrentSlug("");
    this.setState({ modalContent: null });
  };

  public render(): React.ReactNode {
    const curSlug = RenderUtils.GetCurrentSlug();

    if (!this.state.modalContent) {
      return null;
    }
    return (
      <ModalWindow onClose={this.hideModal} width={"80%"} key={`modal_for_${curSlug}`}>
        {this.state.modalContent}
      </ModalWindow>
    );
  }
}
export interface ModalWindowProps {
  onClose: () => void;
  hasCloseButton?: boolean;
  hasCloseIcon?: boolean;
  width?: string;
}

export const ModalWindow: React.FunctionComponent<ModalWindowProps> = (
  props: React.PropsWithChildren<ModalWindowProps>
) => {
  const theme = getTheme();
  const iconButtonStyles: Partial<IButtonStyles> = {
    root: {
      color: theme.palette.neutralPrimary,
      float: "right",
      marginLeft: "auto",
      marginTop: "4px",
      marginRight: "2px",
    },
    rootHovered: {
      color: theme.palette.neutralDark,
    },
  };

  const hostStyles: Partial<IModalStyles> = {
    main: {
      width: props.width,
      overflowY: "hidden",
    },
  };

  const showCloseIcon: boolean = props.hasCloseIcon ?? true;
  const showCloseButton: boolean = props.hasCloseButton ?? true;

  return (
    <Modal styles={hostStyles} isOpen={true} isBlocking={false} onDismiss={props.onClose} data-is-scrollable="true">
      {showCloseIcon && (
        <IconButton
          ariaLabel="Close popup modal"
          styles={iconButtonStyles}
          iconProps={{ iconName: "Cancel" }}
          className="closeIcon"
          onClick={props.onClose}
        ></IconButton>
      )}

      {props.children}

      {showCloseButton && (
        <div className="modalFooter">
          <DefaultButton className="closeButton" href="#" onClick={props.onClose} text="Close" />
        </div>
      )}
    </Modal>
  );
};
