import React, { Component } from "react";
import SyncOverAsyncSection from "../Sections/SyncOverAsyncSection";
import { OtherResultsSection } from "../Sections/OtherResultsSection";
import { Analysis } from "ResultsParser/src/Analysis";
import { HighlightedResultType } from "ResultsParser/src/HighlightedResult";
import { SyncOverAsyncHighlightedResult } from "ResultsParser/src/SyncOverAsyncHighlightedResult";
import ErrorSection, { ErrorType } from "../Sections/ErrorSection";
import FooterSection from "../Sections/FooterSection";
import ArtifactSummarySection from "../Sections/ArtifactSummarySection";
import { OpenInVs } from "../Utils/OpenInVs";

export interface ResultsProps {
  analysis: Analysis | null;
  artifactUrl: string | null;
  isJsonValid: boolean;
}

export default class Results extends Component<ResultsProps, unknown> {
  render() {
    let syncAsyncResult: SyncOverAsyncHighlightedResult | null = null;

    const artifactSummary = this.props.analysis?.summary;
    let analysisNode = null;
    if (this.props.analysis) {
      if (
        this.props.analysis.highlightedResult?.isValid &&
        this.props.analysis.highlightedResult?.type === HighlightedResultType.SyncOverAsync
      ) {
        syncAsyncResult = this.props.analysis.highlightedResult as SyncOverAsyncHighlightedResult;
      }

      const detailedResults = this.props.analysis.results.map((r) => {
        return r.detailedResult;
      });

      analysisNode = (
        <>
          {!this.props.isJsonValid && <ErrorSection types={[ErrorType.JsonError]} />}
          {this.props.artifactUrl && <OpenInVs artifactUrl={this.props.artifactUrl} />}
          {artifactSummary && <ArtifactSummarySection summary={artifactSummary} />}
          {syncAsyncResult && <SyncOverAsyncSection result={syncAsyncResult} />}
          {detailedResults.length >= 1 && (
            <OtherResultsSection
              key={"OtherResultsSummary"}
              results={detailedResults}
              mainFindingsCount={syncAsyncResult ? syncAsyncResult.findings.length : 0}
            />
          )}
        </>
      );
    }
    return (
      <>
        {analysisNode}
        <FooterSection analyzerVersion={this.props.analysis?.analyzerVersion ?? null} />
      </>
    );
  }
}
