// the results viewer build version. It's automatically set by webpack at build time.
// eslint-disable-next-line no-var
declare var RESULTSVIEWER_VERSION: string | undefined;

export class RenderUtils {
  public static GetSortWeightForSeverity(severity: string) {
    switch (severity.toUpperCase()) {
      case "ERROR":
        return 3;
      case "WARNING":
        return 2;
      case "INFO":
        return 1;

      default:
        return -1;
    }
  }

  public static UpdateCurrentSlug(newSlug: string) {
    const curUrl = new URL(document.location.href);
    if (newSlug) {
      curUrl.hash = "#" + newSlug;
    } else {
      curUrl.hash = "#";
    }
    document.location.href = curUrl.toString();
  }

  public static GetCurrentSlug(): string | null {
    const curUrl = new URL(document.location.href);
    if (!curUrl.hash) {
      return null;
    }
    return curUrl.hash.substring(1);
  }

  public static GetResultsViewerVersion(): string | undefined {
    return RESULTSVIEWER_VERSION;
  }
}
