import React from "react";
import { Stack } from "@fluentui/react";
import { DetailedResult } from "ResultsParser/src/DetailedResult";
import { DetailedResultComponent } from "./DetailedResultComponent";
import { SeverityIcon } from "../Utils/SeverityIcon";

import "./ResultCard.less";
import { RowExpander } from "../Utils/RowExpander";

interface IKnownResultCardProps {
  result: DetailedResult;
  className: string;
  isCollapsible: boolean;
}

let index = 0;

// A card that hasn't been specially edited by anything. Use this for most generated resultCards
export function KnownResultCard(props: IKnownResultCardProps) {
  const header: React.ReactNode = (
    <h2>
      <SeverityIcon level={props.result.severity} />
      {props.result.shortDescription}
    </h2>
  );
  const body: React.ReactNode = (
    <DetailedResultComponent key={"DetailedResult_" + index} id={index} result={props.result}></DetailedResultComponent>
  );
  index++;
  return (
    <ResultCard
      className={props.className}
      headerContent={header}
      bodyContent={body}
      isCollapsible={props.isCollapsible}
    ></ResultCard>
  );
}

export class ResultCard extends React.Component<
  { className: string; headerContent: any; bodyContent: any; isCollapsible?: boolean },
  {
    className: string;
    headerContent: React.ReactNode;
    bodyContent: React.ReactNode;
    isCollapsible: boolean;
    isCollapsed: boolean;
  }
> {
  constructor(props: any) {
    super(props);
    this.state = {
      headerContent: props.headerContent,
      bodyContent: props.bodyContent,
      className: props.className,
      isCollapsible: props.isCollapsible ?? false,
      isCollapsed: false,
    };
  }

  render(): React.ReactNode {
    return this.state.isCollapsible ? (
      <Stack className={"resultCard " + this.state.className} tabIndex={0} role="region">
        <div>
          <RowExpander
            isExpanded={!this.state.isCollapsed}
            data={this}
            onExpand={(o) => {
              (o as ResultCard).setState({ isCollapsed: false });
            }}
            onCollapse={(o) => {
              (o as ResultCard).setState({ isCollapsed: true });
            }}
          ></RowExpander>
          {this.state.headerContent}
        </div>
        <div hidden={this.state.isCollapsed}>{this.state.bodyContent}</div>
      </Stack>
    ) : (
      <Stack className={"resultCard " + this.state.className} tabIndex={0} role="region">
        {this.state.headerContent}
        {this.state.bodyContent}
      </Stack>
    );
  }
}
