/**
 * Possible Markdown fragment types.
 */
export enum MarkdownFragmentType {
  Text,
  Link,
}

/**
 * Base class that defines a generic Markdown fragment.
 */
export abstract class MarkdownFragment {
  private _type: MarkdownFragmentType;

  /**
   * @param {MarkdownFragmentType} type The fragment type.
   */
  constructor(type: MarkdownFragmentType) {
      this._type = type
  }

  /**
   * The fragment type
   *
   * @returns {MarkdownFragmentType} The fragment type.
   */
  public get type(): MarkdownFragmentType {
      return this._type
  }

  /**
   * The visible text
   *
   * @returns {string} The visible text.
   */
  public get value(): string {
      return ""
  }
}

/**
 * Represents a text fragment.
 */
export class MarkdownTextFragment extends MarkdownFragment {
  private _data: string;

  /**
   * @param {string} input The input string.
   */
  constructor(input: string) {
      super(MarkdownFragmentType.Text)
      this._data = input
  }

  /**
   * @inheritdoc
   */
  public get value(): string {
      return this._data
  }
}

/**
 * Represents a link fragment.
 */
export class MarkdownLinkFragment extends MarkdownFragment {
  private _content: string;
  private _uri: URL;

  /**
   * @param {URL} uri The link's Uri.
   * @param {string} content The link's visible text.
   */
  constructor(uri: URL, content: string) {
      super(MarkdownFragmentType.Link)
      this._uri = uri
      this._content = content
  }

  /**
   * @inheritdoc
   */
  public get value(): string {
      return this._content
  }

  /**
   * The fragment's Uri.
   *
   * @returns {URL} The Uri.
   */
  public get link(): URL {
      return this._uri
  }
}

/**
 * Represents a text composed from Markdown fragments.
 */
export class MarkdownText {
    private _originalText: string
    private _fragments: MarkdownFragment[] = [];

    /**
     * @param {string} input The input text
     */
    constructor(input: string) {
        this._originalText = input
        this._fragments = parseMarkdown(input)
    }

    /**
     * Array of the composing Markdown fragments.
     *
     * @returns {MarkdownFragment[]} The fragments array.
     */
    public get fragments(): MarkdownFragment[] {
        return this._fragments
    }

    /**
     * The original text, before it was parsed.
     *
     * @returns {string} The original text.
     */
    public get originalText(): string {
        return this._originalText
    }

    /**
     * The visible text, excluding all links or any other fragment types
     *
     * @returns {string} The visible text.
     */
    public get visibleText(): string {
        return this._fragments.map((f) => f.value).join("")
    }
}

/**
 * Parse Markdown fragments from plaintext.
 *
 * @param {string} input The input plaintext.
 * @returns {MarkdownFragment[]} An array of Markdown fragments.
 */
export function parseMarkdown(input: string): MarkdownFragment[] {
    const LinkUrlStartToken = "("
    const LinkUrlEndToken = ")"
    const LinkTextStartToken = "["
    const LinkTextEndToken = "]"
    const EscapeToken = "\\"

    const fragments: MarkdownFragment[] = []
    let curFragmentStartPos = 0
    let curLinkText = ""
    let linkUrlStartPos = 0
    let linkStartPos = 0
    let alreadyParseLinkContent = false
    let curFragmentHasEscapeTokens = false
    const textStartTokenPosStack: number[] = []

    const RemoveEscapeTokens = (text: string, textIncludesEscapeTokens: boolean) => {
        if (textIncludesEscapeTokens) {
            return text.replace(/\\[[\](\\)]/g, (match) => {
                return match.substr(1) // ignore the '\'
            })
        }
        return text
    }

    const AddTextFragment = (fragments: MarkdownFragment[], text: string, textIncludesEscapeTokens: boolean) => {
        if (fragments.length >= 1 && fragments[fragments.length - 1].type === MarkdownFragmentType.Text) {
            const newFragment = new MarkdownTextFragment(
                fragments[fragments.length - 1].value + RemoveEscapeTokens(text, textIncludesEscapeTokens)
            )
            fragments[fragments.length - 1] = newFragment
        } else {
            fragments.push(new MarkdownTextFragment(RemoveEscapeTokens(text, textIncludesEscapeTokens)))
        }
    }

    for (let idx = 0; idx < input.length; idx++) {
        switch (input[idx]) {
            case EscapeToken: {
                curFragmentHasEscapeTokens = true
                if (idx < input.length - 1 && "\\()[]".indexOf(input[idx + 1]) !== -1) {
                    idx++
                }
                break
            }
            case LinkTextStartToken: {
                textStartTokenPosStack.push(idx)
                break
            }
            case LinkTextEndToken: {
                if (idx < input.length - 1 && input[idx + 1] === LinkUrlStartToken && textStartTokenPosStack.length > 0) {
                    linkStartPos = textStartTokenPosStack[textStartTokenPosStack.length - 1]
                    curLinkText = RemoveEscapeTokens(input.substring(linkStartPos + 1, idx), curFragmentHasEscapeTokens)
                    idx++
                    alreadyParseLinkContent = true
                    linkUrlStartPos = idx
                }

                if (textStartTokenPosStack.length > 0) {
                    textStartTokenPosStack.pop()
                }
                break
            }
            case LinkUrlEndToken: {
                if (!alreadyParseLinkContent) {
                    break
                }
                if (curFragmentStartPos < linkStartPos) {
                    AddTextFragment(fragments, input.substring(curFragmentStartPos, linkStartPos), curFragmentHasEscapeTokens)
                    curFragmentHasEscapeTokens = false
                }
                try {
                    if (curLinkText.length === 0) {
                        throw new Error("The link content is empty")
                    }
                    const uri = new URL(input.substring(linkUrlStartPos + 1, idx))
                    fragments.push(new MarkdownLinkFragment(uri, curLinkText))
                } catch (e) {
                    // invalid URI, consider it a text fragment
                    linkStartPos = -1
                    if (curLinkText.length > 0) {
                        AddTextFragment(fragments, curLinkText, curFragmentHasEscapeTokens)
                        curFragmentHasEscapeTokens = false
                    }
                }
                alreadyParseLinkContent = false
                curFragmentStartPos = idx + 1
                break
            }
            default:
                break
        }
    }

    if (curFragmentStartPos < input.length) {
        AddTextFragment(fragments, input.substring(curFragmentStartPos), curFragmentHasEscapeTokens)
    }

    return fragments
}