import { ArtifactSummary, ArtifactSummaryType } from "./ArtifactSummary"
import type { RawResult } from "./Analysis"
import { TimeSpan } from "./TimeSpan"

interface TraceSummaryResult extends RawResult {
    FileName: string,
    HasCallStacks: boolean,
    ProcessId: number,
    ProcessName: string,
    ProcessBitness: string,
    SessionStartLocalTime: string,
    SessionDuration: string,
    MachineName: string,
    CpuCount: number
}

/**
 * Represents a trace summary.
 */
export class TraceSummary implements ArtifactSummary {
    /**
     * @param {RawResult} result The raw result.
     */
    constructor(result: RawResult) {
        if (ArtifactSummary.getArtifactSummaryType(result) !== ArtifactSummaryType.Trace) {
            throw new Error("The result is not supported!")
        }

        this.#_result = result as TraceSummaryResult
    }

    /**
     * @inheritdoc
     */
    public get result(): RawResult {
        return this.#_result
    }

    /**
     * @inheritdoc
     */
    public get artifactSummaryType(): ArtifactSummaryType {
        return ArtifactSummaryType.Trace
    }

    /**
     * @returns {string} The filename of the trace.
     */
    public get fileName(): string {
        return this.#_result.FileName
    }

    /**
     * @returns {boolean} Whether the trace has callstacks.
     */
    public get hasCallStacks(): boolean {
        return this.#_result.HasCallStacks
    }

    /**
     * @returns {number} The process id.
     */
    public get processId(): number {
        return this.#_result.ProcessId
    }

    /**
     * @returns {string} The process name.
     */
    public get processName(): string {
        return this.#_result.ProcessName
    }

    /**
     * @returns {string} The process bitness.
     */
    public get processBitness(): string {
        return this.#_result.ProcessBitness
    }

    /**
     * @returns {string} The starting datetime of the session.
     */
    public get sessionStart(): string {
        return this.#_result.SessionStartLocalTime
    }

    /**
     * @returns {TimeSpan} The overall duration of the session.
     */
    public get sessionDuration(): TimeSpan {
        if (this.#_result.SessionDuration) {
            const duration = TimeSpan.fromNETTimeSpanStringInvariant(this.#_result.SessionDuration)
            if (duration.isValid) {
                return duration
            }
        }

        return TimeSpan.fromMilliseconds(0)
    }

    /**
     * @returns {string} The machine name.
     */
    public get machineName(): string {
        return this.#_result.MachineName
    }

    /**
     * @returns {number} The number of processors.
     */
    public get cpuCount(): number {
        return this.#_result.CpuCount
    }

    #_result: TraceSummaryResult
}