import React, { Component } from "react";
import { RowExpander } from "./RowExpander";

export interface KeyValueEntry {
  key: string;
  value: string | undefined;
}

export interface KeyValueListProps {
  headerText: string;
  entries: KeyValueEntry[];
  isInitiallyCollapsed: boolean;
  rowClassName: string;
  keyClassName: string;
  valueClassName: string;
}

interface KeyValueListState {
  isCollapsed: boolean;
}

export default class KeyValueList extends Component<KeyValueListProps, KeyValueListState> {
  constructor(props: KeyValueListProps) {
    super(props);

    this.state = {
      isCollapsed: props.isInitiallyCollapsed,
    };
  }

  render() {
    let header;
    if (this.props.headerText.length !== 0) {
      header = (
        <>
          <h2>
            <RowExpander
              isExpanded={!this.state.isCollapsed}
              data={this}
              onExpand={(o) => {
                (o as KeyValueList).setState({ isCollapsed: false });
              }}
              onCollapse={(o) => {
                (o as KeyValueList).setState({ isCollapsed: true });
              }}
            />
            {this.props.headerText}
          </h2>
          <hr />
        </>
      );
    }
    return (
      <>
        {header}
        {!this.state.isCollapsed &&
          this.props.entries.map((e, index) => {
            return (
              <div key={`keyval_${this.props.headerText}_${index}`} tabIndex={0} className={this.props.rowClassName}>
                <div className={this.props.keyClassName}>{e.key}</div>
                <div className={this.props.valueClassName}>
                  <code>{e.value}</code>
                </div>
              </div>
            );
          })}
      </>
    );
  }
}
