import { DefaultButton } from "@fluentui/react";
import React, { Component } from "react";
import "./SurveyPrompt.less";

export interface SurveyPromptProps {
  survey: string;
  label?: string;
  errorCodes?: string[];
}

export default class SurveyPrompt extends Component<SurveyPromptProps, unknown> {
  render() {
    const labelText = this.props.label ?? "Did you find this information useful?";
    let surveyLink = `https://aka.ms/autoanalysis/${this.props.survey.toLowerCase()}`;

    if (this.props.survey === "general" && this.props.errorCodes && this.props.errorCodes.length >= 1) {
      // Include the error code in the link path
      // The error code format is 'AAnnnn', where nnnn is a number.
      // We currently have custom links just for error codes up to AA0031. For anything higher use the default url.
      const errorCodeVal = parseInt(this.props.errorCodes[0].substring(2));
      if (errorCodeVal >= 1 && errorCodeVal <= 31) {
        surveyLink = `https://aka.ms/autoanalysis/${this.props.errorCodes[0].toUpperCase()}`;
      }
    }

    const errorCodes = this.props.errorCodes ? "?aa=" + this.props.errorCodes.sort().join(",") : "";
    const helpful = errorCodes !== "" ? "&helpful=" : "?helpful=";

    const yesLink = surveyLink + "/yes" + errorCodes + helpful + "y";
    const noLink = surveyLink + "/no" + errorCodes + helpful + "n";

    return (
      <div className="survey" role="region" tabIndex={0}>
        <label>{labelText}</label>
        <DefaultButton className="surveyButton" href={yesLink} target="_blank" text="Yes" />
        <DefaultButton className="surveyButton" href={noLink} target="_blank" text="No" />
      </div>
    );
  }
}
