import { Link } from "@fluentui/react";
import React from "react";
import { DetailedResult } from "ResultsParser/src/DetailedResult";
import { Navigator } from "./Navigator";
import { KnownResultCard } from "../CardContent/ResultCard";
import "../CardContent/ResultCard.less";

interface IOtherResultsProps {
  result: DetailedResult;
  index: number;
}

export class OtherResult extends React.Component<
  IOtherResultsProps,
  { result: DetailedResult; index: number; showModal: boolean }
> {
  constructor(props: IOtherResultsProps) {
    super(props);

    this.state = {
      result: props.result,
      showModal: false,
      index: props.index,
    };

    Navigator.registerRoute(this.slug, props.result.errorCode, () => {
      return this.renderResultCard();
    });
  }

  private get slug(): string {
    return "result-" + this.state.index;
  }

  private showModal = () => {
    Navigator.showModal(this.slug);
  };

  private renderResultCard(): React.ReactNode {
    return <KnownResultCard result={this.state.result} className={""} isCollapsible={false}></KnownResultCard>;
  }

  render(): React.ReactNode {
    return (
      <Link aria-label={this.state.result.shortDescription} className="otherResultFinding" onClick={this.showModal}>
        {this.state.result.shortDescription}
      </Link>
    );
  }
}
