import React from "react";
import { AssetsCollection } from "ResultsParser/src/Assets";
import { ClrModuleAsset, ThreadAsset } from "../../AnalysisResults";
import { ClrModule } from "./ClrModule";
import { Thread } from "./Thread";

export class AssetFactory {
  public static createAssetComponent(
    id: string,
    assetLink: URL,
    assets: AssetsCollection
  ): [displayName: string, node: React.ReactNode] {
    const assetObj = assets.findAsset(assetLink);
    if (assetObj === undefined) {
      return ["", undefined];
    }

    switch (assetLink.protocol) {
      case "clrmodule:": {
        const moduleObj = assetObj as ClrModuleAsset;
        if (moduleObj === undefined) {
          break;
        }

        return [ClrModule.displayName(moduleObj), <ClrModule id={`clrModule_${id}`} moduleObj={moduleObj} />];
      }
      case "thread:": {
        const threadObj = assetObj as ThreadAsset;
        if (threadObj === undefined) {
          break;
        }

        return [
          Thread.displayName(threadObj),
          <Thread id={`thread_${id}`} assetLink={assetLink} threadObj={threadObj} />,
        ];
      }
    }

    return ["", undefined];
  }
}
