/**
 *
 */
export class AssetsCollection {
    private assetJson: any;

    constructor(json: any) {
        this.assetJson = json
    }

    public static parseAssets(json: any): AssetsCollection {
        // TODO: expand assets to do better parsing here later
        return new AssetsCollection(json)
    }

    public findAsset(input: URL): any {
        if (this.assetJson === undefined) {
            return undefined
        }

        const type = input.protocol.substring(0, input.protocol.length - 1)
        if (!(type in this.assetJson)) {
            return undefined
        }

        const id = input.pathname
        if (!(id in this.assetJson[type])) {
            return undefined
        }

        return this.assetJson[type][id]
    }
}