import React from "react";

import "./NoticeCard.less";
import { RowExpander } from "../Utils/RowExpander";

// NOTE: the checked-in NOTICE.json is empty because the content will be
// generated at build time.
import * as notices from "./NOTICE.json";

interface License {
  title: string;
  link: string;
  license: string[];
}

interface Licenses {
  preamble: string[];
  licenses: License[];
}

export class LicenseCard extends React.Component<License, { isCollapsed: boolean }> {
  constructor(props: License) {
    super(props);
    this.state = { isCollapsed: true };
  }
  render(): React.ReactNode {
    return (
      <div className="noticeCard">
        <h3>
          <RowExpander
            isExpanded={!this.state.isCollapsed}
            data={this}
            onExpand={(o) => {
              (o as LicenseCard).setState({ isCollapsed: false });
            }}
            onCollapse={(o) => {
              (o as LicenseCard).setState({ isCollapsed: true });
            }}
          ></RowExpander>
          {this.props.title}
        </h3>
        <div hidden={this.state.isCollapsed}>
          {this.props.link && (
            <p>
              <a href={this.props.link} target="_blank" rel="noopener noreferrer">
                {this.props.link}
              </a>
            </p>
          )}
          {this.props.license.map((s, i) => (
            <p key={`ll_${i}`}>{s}&nbsp;</p>
          ))}
        </div>
      </div>
    );
  }
}

export class NoticeCard extends React.Component<unknown, unknown> {
  render(): React.ReactNode {
    const licenceNotice: Licenses = notices as Licenses;
    return (
      <>
        <div className={"noticePreamble"}>
          <h2>{licenceNotice.preamble[0]}</h2>
          {licenceNotice.preamble.slice(1).map((s, index) => (
            <p key={`pa_${index}`}>{s}&nbsp;</p>
          ))}
        </div>
        <div className={"noticeNotices"}>
          {licenceNotice.licenses.map((n, index) => (
            <LicenseCard {...n} key={`lc_${index}`} />
          ))}
        </div>
      </>
    );
  }
}
