import React from "react";
import {
  DetailedResult,
  DetailItem,
  DetailItemType,
  RegionItem,
  TextItem,
  TreeGridItem,
} from "ResultsParser/src/DetailedResult";
import { TreeGridList } from "../Utils/TreeGridList";
import { MarkdownFragmentComponent } from "../Utils/MarkdownFragmentComponent";
import { MarkdownFragment } from "ResultsParser/src/MarkdownParser";
import SurveyPrompt from "../Utils/SurveyPrompt";
import "./DetailedResult.less";

interface IResultState {
  id: number;
  result: DetailedResult;
}

interface IDetailedResultComponentProps {
  id: number;
  result: DetailedResult;
}

interface IInnerResultProps {
  id: string;
  innerItem: DetailItem;
}

export class DetailedResultComponent extends React.Component<IDetailedResultComponentProps, IResultState> {
  constructor(props: IDetailedResultComponentProps) {
    super(props);
    this.state = { id: props.id, result: props.result };
  }

  render(): React.ReactNode {
    const resultId = "result_index_" + this.state.id;
    const itemSectionId = "itemSection_" + this.state.id;

    return (
      <div className="detailedResult" id={resultId}>
        <div id={itemSectionId}>
          {this.state.result.items.map((innerResult, index) => {
            return (
              <InnerResult
                key={"inner_item" + this.state.id + "_" + index}
                id={"inner_item" + this.state.id + "_" + index}
                innerItem={innerResult}
              ></InnerResult>
            );
          })}
        </div>
        <SurveyPrompt survey="general" label="Was this helpful?" errorCodes={[this.props.result.errorCode]} />
      </div>
    );
  }
}

class InnerResult extends React.Component<IInnerResultProps, { id: string; innerItem: DetailItem }> {
  constructor(props: IInnerResultProps) {
    super(props);

    this.state = { id: props.id, innerItem: props.innerItem };
  }

  render(): React.ReactNode {
    switch (this.state.innerItem.type) {
      case DetailItemType.Region: {
        const regionItem: RegionItem = this.state.innerItem as RegionItem;
        return (
          <div id={this.state.id}>
            <div className="RegionHeader">
              <span>{regionItem.header}</span>
            </div>
            <div className="RegionContent">
              {regionItem.items.map((item, index) => {
                return (
                  <InnerResult
                    key={this.state.id + "_" + index}
                    id={this.state.id + "_" + index}
                    innerItem={item}
                  ></InnerResult>
                );
              })}
            </div>
          </div>
        );
      }
      case DetailItemType.Text: {
        const textItem: TextItem = this.state.innerItem as TextItem;

        const fragmentNodes = textItem.text.fragments.map((fragment: MarkdownFragment, index: number) => {
          const id = `${this.state.id}_md_${index}`;
          return <MarkdownFragmentComponent key={id} id={id} fragment={fragment} />;
        });
        return <span className="TextItem">{fragmentNodes}</span>;
      }
      case DetailItemType.Tree: {
        const treeGridItem: TreeGridItem = this.state.innerItem as TreeGridItem;
        return <TreeGridList id={this.state.id} treeGridItem={treeGridItem} />;
      }
    }
  }
}
