import { DetailsList, DetailsListLayoutMode, IColumn, SelectionMode } from "@fluentui/react";
import React from "react";
import { DetailedResult } from "ResultsParser/src/DetailedResult";
import { OtherResult } from "../Utils/OtherResult";
import { SeverityIcon } from "../Utils/SeverityIcon";
import { Navigator } from "../Utils/Navigator";

import "./Styles/OtherResultsSection.less";
import "./Styles/FrontPageCard.less";
import { ResultCard } from "../CardContent/ResultCard";

interface IResultsColumn extends IColumn {
  columnIndex: number;
}

interface IOtherResultsProps {
  results: DetailedResult[];
  mainFindingsCount: number;
}

export class OtherResultsSection extends React.Component<
  IOtherResultsProps,
  { results: DetailedResult[]; mainFindingsCount: number }
> {
  constructor(props: IOtherResultsProps) {
    super(props);

    this.state = {
      mainFindingsCount: props.mainFindingsCount,
      results: props.results,
    };
  }

  private renderItemColumn(
    result?: DetailedResult,
    rowIndex?: number | undefined,
    column?: IColumn | undefined
  ): JSX.Element {
    if (!column) {
      return null as unknown as JSX.Element;
    }
    const thisObj = column.data;
    if (!thisObj) {
      return null as unknown as JSX.Element;
    }
    if (!result) {
      return null as unknown as JSX.Element;
    }

    switch ((column as IResultsColumn).columnIndex) {
      case 0:
        return (
          <div aria-label={result.severity} className="otherResultSeverity">
            <SeverityIcon level={result.severity} />
            <label>{result.severity}</label>
          </div>
        );
      case 1:
        return <OtherResult result={result} index={rowIndex as number}></OtherResult>;
    }
    return null as unknown as JSX.Element;
  }

  render(): React.ReactNode {
    const findingsString = this.state.mainFindingsCount != 0 ? "Other Findings" : "Findings";

    const columns: IResultsColumn[] = [
      {
        key: "OtherResults_Severity",
        name: "Severity",
        columnIndex: 0,
        fieldName: "Severity",
        minWidth: 100,
        flexGrow: 10,
        isResizable: true,
        data: this,
      },
      {
        key: "OtherResults_Finding",
        name: "Finding",
        columnIndex: 1,
        fieldName: "Finding",
        minWidth: 250,
        flexGrow: 90,
        isResizable: true,
        data: this,
      },
    ];

    const header: React.ReactNode = <h2 role="heading">{findingsString}</h2>;
    const body: React.ReactNode = (
      <DetailsList
        ariaLabelForGrid="Other Results"
        items={this.state.results}
        columns={columns}
        selectionMode={SelectionMode.none}
        onRenderItemColumn={this.renderItemColumn}
        onItemInvoked={this.onItemInvoked}
        layoutMode={DetailsListLayoutMode.fixedColumns}
        className="otherResultsList"
      />
    );

    return (
      <ResultCard
        className="resultCard content front-page-card"
        headerContent={header}
        bodyContent={body}
        isCollapsible={true}
      ></ResultCard>
    );
  }

  private onItemInvoked = (item?: DetailedResult, index?: number) => {
    Navigator.showModal("result-" + index);
  };
}
