import { ArtifactSummary, ArtifactSummaryType } from "./ArtifactSummary"
import type { RawResult } from "./Analysis"
import { TimeSpan } from "./TimeSpan"

interface AzureAppServiceSummary {
    MachineName: string;
    SiteName: string;
    PlatformVersion: string;
}

export enum MemoryDumpCollectionTrigger {
    Unknown,
    AutoHealTotalRequests,
    AutoHealMemory,
    AutoHealStatusCode,
    AutoHealSlowRequests,
    AutoHealPercentMemory,
    AutoHealPercentSlowRequests,
    AutoHealStartupFailures,
}

interface DumpSummaryResult extends RawResult {
    ProcessName: string;
    ProcessArgs: string;
    ProcessUptime: string;
    ProcessArchitecture: string;
    CPUCount: number;
    HasHeapInformation: boolean;
    ClrVersion: string;
    AzureAppServiceInfo: AzureAppServiceSummary;
    CollectionTrigger: MemoryDumpCollectionTrigger;
}

/**
 * Represents a memory dump summary.
 */
export class MemoryDumpSummary implements ArtifactSummary {
    /**
     * @param {RawResult} result The raw result.
     */
    constructor(result: RawResult) {
        if (ArtifactSummary.getArtifactSummaryType(result) !== ArtifactSummaryType.MemoryDump) {
            throw new Error("The result is not supported!")
        }
        this.#_result = result as DumpSummaryResult

        if ("CollectionTrigger" in result) {
            // eslint-disable-next-line @typescript-eslint/no-unsafe-member-access, @typescript-eslint/no-explicit-any
            switch (((result as any).CollectionTrigger as string).toLowerCase()) {
                case "ah_total_req":
                    this.#_result.CollectionTrigger = MemoryDumpCollectionTrigger.AutoHealTotalRequests
                    break
                case "ah_memory":
                    this.#_result.CollectionTrigger = MemoryDumpCollectionTrigger.AutoHealMemory
                    break
                case "ah_status_code":
                    this.#_result.CollectionTrigger = MemoryDumpCollectionTrigger.AutoHealStatusCode
                    break
                case "ah_slow":
                    this.#_result.CollectionTrigger = MemoryDumpCollectionTrigger.AutoHealSlowRequests
                    break
                case "ah_memory_percent":
                    this.#_result.CollectionTrigger = MemoryDumpCollectionTrigger.AutoHealPercentMemory
                    break
                case "ah_slow_percent":
                    this.#_result.CollectionTrigger = MemoryDumpCollectionTrigger.AutoHealPercentSlowRequests
                    break
                case "ah_startup":
                    this.#_result.CollectionTrigger = MemoryDumpCollectionTrigger.AutoHealStartupFailures
                    break
                default:
                    this.#_result.CollectionTrigger = MemoryDumpCollectionTrigger.Unknown
                    break
            }
        } else {
            this.#_result.CollectionTrigger = MemoryDumpCollectionTrigger.Unknown
        }
    }

    /**
     * @inheritdoc
     */
    public get result(): RawResult {
        return this.#_result
    }

    /**
     * @inheritdoc
     */
    public get artifactSummaryType(): ArtifactSummaryType {
        return ArtifactSummaryType.MemoryDump
    }

    /**
     * The process path.
     *
     * @returns {string} The process path.
     */
    public get processPath(): string {
        return this.#_result.ProcessName
    }

    /**
     * The process arguments.
     *
     * @returns {string} The process arguments.
     */
    public get processArgs(): string {
        return this.#_result.ProcessArgs
    }

    /**
     * The process architecture.
     *
     * @returns {string} The process architecture.
     */
    public get processArchitecture(): string {
        return this.#_result.ProcessArchitecture
    }

    /**
     * The process uptime.
     *
     * @returns {TimeSpan} The process uptime.
     */
    public get processUptime(): TimeSpan {
        if (this.#_result.ProcessUptime) {
            const duration = TimeSpan.fromNETTimeSpanStringInvariant(this.#_result.ProcessUptime)
            if (duration.isValid) {
                return duration
            }
        }

        return TimeSpan.fromMilliseconds(0)
    }

    /**
     * The number of processors.
     *
     * @returns {number} The number of processors.
     */
    public get cpuCount(): number {
        return this.#_result.CPUCount
    }

    /**
     * Has heap information?
     *
     * @returns {boolean} Has heap information?
     */
    public get hasHeapInformation(): boolean {
        return this.#_result.HasHeapInformation
    }

    /**
     * CLR version.
     *
     * @returns {string} CLR version.
     */
    public get clrVersion(): string {
        if (this.#_result.ClrVersion && this.#_result.ClrVersion.length > 1 && this.#_result.ClrVersion[0] === "v") {
            return this.#_result.ClrVersion.slice(1)
        }
        return this.#_result.ClrVersion
    }

    /**
     * Was the memory dump collected from an Azure App Service?
     *
     * @returns {boolean}  Was the memory dump collected from an Azure App Service?
     */
    public get isAzureAppService(): boolean {
        return !!this.#_result.AzureAppServiceInfo
    }

    /**
     * AppService site name. It's empty if isAzureAppService returns false
     *
     * @returns {string} AppService site name.
     */
    public get appServiceSiteName(): string {
        return this.isAzureAppService && this.#_result.AzureAppServiceInfo.SiteName ? this.#_result.AzureAppServiceInfo.SiteName : ""
    }

    /**
     * AppService machine name. It's empty if isAzureAppService returns false
     *
     * @returns {string} AppService machine name.
     */
    public get appServiceMachineName(): string {
        return this.isAzureAppService && this.#_result.AzureAppServiceInfo.MachineName ? this.#_result.AzureAppServiceInfo.MachineName : ""
    }

    /**
     * AppService platform version. It's empty if isAzureAppService returns false
     *
     * @returns {string} AppService platform version.
     */
    public get appServicePlatformVersion(): string {
        return this.isAzureAppService && this.#_result.AzureAppServiceInfo.PlatformVersion ? this.#_result.AzureAppServiceInfo.PlatformVersion : ""
    }

    /**
     * The memory dump collection trigger.
     *
     * @returns {MemoryDumpCollectionTrigger} The trigger.
     */
    public get collectionTrigger(): MemoryDumpCollectionTrigger {
        return this.#_result.CollectionTrigger
    }

    #_result: DumpSummaryResult
}