import {
  Link,
  SearchBox,
  IIconProps,
  List,
  Announced,
  FocusZone,
  FocusZoneDirection,
  Toggle,
  MessageBarType,
  MessageBar,
} from "@fluentui/react";
import React from "react";
import { StackFrameAsset } from "../../AnalysisResults";
import "./Asset.less";
import "./StackTrace.less";

export interface StackTraceProps {
  id: string;
  defaultJMC: boolean;
  stackTrace: StackFrameAsset[];
  frameBaseAddressToFocusOn: string | undefined;
}

interface StackFrameIndex {
  index: number | undefined;
  jmcIndex: number | undefined;
}

interface StackTraceState {
  frameToIndex: Map<StackFrameAsset, StackFrameIndex>;

  jmcStackTrace: StackFrameAsset[];
  framesToRender: StackFrameAsset[];
  jmc: boolean;
  areSymbolsMissing: boolean;
  filterText: string | undefined;
  filterDetails: string;
  showCodeNavigationWarning: boolean;
}

export class StackTrace extends React.Component<StackTraceProps, StackTraceState> {
  constructor(props: StackTraceProps) {
    super(props);

    const stackTrace = props.stackTrace;
    const jmcStackTrace = [];
    let alreadyInNonUserCode = false;
    let frameIndexToFocusOn: number | undefined;
    let areSymbolsMissing = false;

    const frameToIndex = new Map<StackFrameAsset, StackFrameIndex>();

    for (let i = 0; i < stackTrace.length; i++) {
      const frameIndex: StackFrameIndex = { index: i, jmcIndex: undefined };
      const frame = stackTrace[i];

      if (
        this.props.frameBaseAddressToFocusOn !== undefined &&
        frame.baseAddress === this.props.frameBaseAddressToFocusOn
      ) {
        frameIndexToFocusOn = i;
      }

      if (frame.ilPosition !== undefined) {
        if (!frame.ilPosition.hasSymbols && frame.ilPosition.isUserCode) {
          areSymbolsMissing = true;
        }

        if (frame.ilPosition.isUserCode) {
          alreadyInNonUserCode = false;
          frameIndex.jmcIndex = jmcStackTrace.length;
          jmcStackTrace.push(frame);
        } else if (!alreadyInNonUserCode) {
          const collapsedFrame: StackFrameAsset = { name: "[External Code]" };
          const collapsedFrameIndex: StackFrameIndex = { index: undefined, jmcIndex: jmcStackTrace.length };
          alreadyInNonUserCode = true;
          jmcStackTrace.push(collapsedFrame);
          frameToIndex.set(collapsedFrame, collapsedFrameIndex);
        }
      }

      frameToIndex.set(frame, frameIndex);
    }

    let jmc = this.props.defaultJMC;
    if (jmc && frameIndexToFocusOn !== undefined) {
      jmc = stackTrace[frameIndexToFocusOn].ilPosition?.isUserCode === true;
    }

    const defaultStackTrace = jmc ? jmcStackTrace : stackTrace;
    this.state = {
      frameToIndex: frameToIndex,
      jmcStackTrace: jmcStackTrace,
      framesToRender: defaultStackTrace,
      jmc: jmc,
      filterText: undefined,
      areSymbolsMissing: areSymbolsMissing,
      showCodeNavigationWarning: areSymbolsMissing,
      filterDetails: this._getFilterDetails(defaultStackTrace.length, defaultStackTrace.length),
    };
  }

  private _getFilterDetails = (filteredItems: number, unfilteredItems: number): string => {
    if (filteredItems === unfilteredItems) {
      return `Showing all ${unfilteredItems.toLocaleString()} stack frames`;
    } else if (filteredItems === 0) {
      return `No matching stack frames found`;
    } else {
      return `Showing ${filteredItems.toLocaleString()}/${unfilteredItems.toLocaleString()} stack frames`;
    }
  };

  private _onFilter = (event?: React.ChangeEvent<HTMLInputElement>, text?: string, newJMC?: boolean): void => {
    const effectiveJMC = newJMC ?? this.state.jmc;

    const unfilteredItems = effectiveJMC ? this.state.jmcStackTrace : this.props.stackTrace;
    const filteredItems = text
      ? unfilteredItems.filter((i) => i.name.toLowerCase().indexOf(text.toLowerCase()) > -1)
      : unfilteredItems;

    const filterDetails = this._getFilterDetails(filteredItems.length, unfilteredItems.length);

    this.setState({
      framesToRender: filteredItems,
      filterText: text,
      filterDetails: filterDetails,
      jmc: effectiveJMC,
    });
  };

  private _onJMC = (event: React.MouseEvent<HTMLElement>, checked?: boolean): void => {
    // When JMC is changed the frame filtering also needs to be recalculated against the new
    // stacktrace.
    //
    // We cannot simply set the JMC state here and re-run the filtering, since set setState
    // is asynchronous and is independently scheduled by React.
    //
    // We could rely on a state callback, but then we would need to go through 2 independent component update
    // cycles in React, so instead let _onFilter also take care of setting the new JMC state.
    this._onFilter(undefined, this.state.filterText, checked === true);
  };

  private _onRenderCell = (item?: StackFrameAsset, index?: number, isScrolling?: boolean): JSX.Element => {
    if (item === undefined) {
      return <></>;
    }

    const uri = item.ilPosition?.sourceCodeInfo?.uri;
    const frameIndexes = this.state.frameToIndex.get(item);
    if (frameIndexes === undefined) {
      return <></>;
    }

    const unfilteredIndex = this.state.jmc === true ? frameIndexes.jmcIndex : frameIndexes.index;
    if (unfilteredIndex === undefined) {
      return <></>;
    }

    let content;
    if (uri === undefined) {
      content = item.name;
    } else {
      content = (
        <Link href={uri} target="_blank">
          <code>{item.name}</code>
        </Link>
      );
    }

    const isFocused =
      this.props.frameBaseAddressToFocusOn !== undefined && item.baseAddress === this.props.frameBaseAddressToFocusOn;
    let className = "row-content";
    if (isFocused) {
      className += " selected-row";
    }

    return (
      <div data-is-focusable>
        <div className={className} aria-selected={isFocused}>
          <code>
            {unfilteredIndex + 1}&nbsp;&nbsp;{content}
          </code>
        </div>
      </div>
    );
  };

  render(): React.ReactNode {
    const filterIcon: IIconProps = { iconName: "Filter" };
    return (
      <>
        {this.state.showCodeNavigationWarning && (
          <>
            <MessageBar
              messageBarType={MessageBarType.warning}
              isMultiline={true}
              onDismiss={() => {
                this.setState({ showCodeNavigationWarning: false });
              }}
              dismissButtonAriaLabel="Close"
            >
              Some stack frames are missing source link information, code navigation will not be available everywhere.{" "}
              <Link href="https://aka.ms/autoanalysis/sourcelink/learnmore" target="_blank">
                Learn more.
              </Link>
            </MessageBar>
            <br></br>
          </>
        )}
        <FocusZone direction={FocusZoneDirection.vertical}>
          <div>
            <Toggle
              label="Just My Code"
              onText="On"
              offText="Off"
              inlineLabel
              checked={this.state.jmc}
              onChange={this._onJMC}
            />
            <SearchBox placeholder="Filter by stack frame name" iconProps={filterIcon} onChange={this._onFilter} />
            <span>{this.state.filterDetails}</span>
            <Announced message={this.state.filterDetails} />
          </div>
          {this.state.framesToRender.length !== 0 && (
            <div className="stack-trace-container" data-is-scrollable>
              <List items={this.state.framesToRender} onRenderCell={this._onRenderCell}></List>
            </div>
          )}
        </FocusZone>
      </>
    );
  }
}
