import { MarkdownText } from "./MarkdownParser"
import { TimeSpan } from "./TimeSpan"

/**
 * Possible detailed item types.
 */
export enum DetailItemType {
  Text,
  Region,
  Tree,
}

/**
 * Base class that defines a generic DetailItem.
 */
export abstract class DetailItem {
  private _type: DetailItemType;
  private _id: string;

  /**
   * @param {DetailItemType} type The DetailItem type.
   */
  constructor(type: DetailItemType) {
      this._type = type
      this._id = `${this.typeName}_${(DetailItem.idCounter++).toString()}`
  }

  /**
   * The item's type name.
   *
   * @returns {string} The type name.
   */
  private get typeName(): string {
      switch (this._type) {
          case DetailItemType.Text:
              return "TextItem"
          case DetailItemType.Region:
              return "RegionItem"
          case DetailItemType.Tree:
              return "TreeItem"
          default:
              throw new Error("Unknown item type!")
      }
  }

  /**
   * The item's type.
   *
   * @returns {DetailItemType} The item type.
   */
  public get type(): DetailItemType {
      return this._type
  }

  /**
   * The item's unique id.
   *
   * @returns {string} The item id.
   */
  public get id(): string {
      return this._id
  }

  private static idCounter = 0;
}

/**
 * Represents a detailed result.
 */
export class DetailedResult {
  private _items: DetailItem[] = [];
  private _shortDescription = "";
  private _errorCode = "";
  private _severity = "";

  // eslint-disable-next-line jsdoc/no-undefined-types
  /**
   * @param {unknown} rawInput The raw input (schema available here: https://dev.azure.com/devdiv/DevDiv/_wiki/wikis/DevDiv.wiki/23255/Detailed-Analysis-results?anchor=json-schema)
   */
  constructor(rawInput: unknown) {
      this._shortDescription = getPropertyOrDefaultFromOpaqueObject(rawInput, "shortDescription", "")
      this._errorCode = getPropertyOrDefaultFromOpaqueObject(rawInput, "errorCode", "")
      this._severity = getPropertyOrDefaultFromOpaqueObject(rawInput, "severity", "")
      const details = getPropertyOrDefaultFromOpaqueObject<Array<unknown>|null>(rawInput, "details", null)
      if (details) {
          this._items = DetailedResult.parseDetailItemArray(details)
      }
  }

  /**
   * Array of containing detail items.
   *
   * @returns {DetailItem[]} the array
   */
  public get items(): DetailItem[] {
      return this._items
  }

  /**
   * The result's short description.
   *
   * @returns {string} The description.
   */
  public get shortDescription(): string {
      return this._shortDescription
  }

  /**
   * The result's error code.
   *
   * @returns {string} The error code.
   */
  public get errorCode(): string {
      return this._errorCode
  }

  /**
   * The result's severity.
   *
   * @returns {string} The severity.
   */
  public get severity(): string {
      return this._severity
  }

  /**
   * A numeric value denoting the severity of the result (higher means more important).
   *
   * @returns {number} The sort weight.
   */
  public get sortWeight(): number {
      switch (this._severity.toUpperCase()) {
          case "ERROR":
              return 3
          case "WARNING":
              return 2
          case "INFO":
              return 1
          default:
              return -1
      }
  }

  /**
   * @private
   */
  private static parseDetailItemArray(rawData: Array<unknown>): DetailItem[] {
      const parsedItems: DetailItem[] = []

      for (const detailedItem of rawData) {
          const parsedItem = DetailedResult.parseDetailItem(detailedItem)
          if (parsedItem !== null) {
              parsedItems.push(parsedItem)
          }
      }
      return parsedItems
  }

  /**
   * @private
   */
  private static parseDetailItem(rawData: unknown): DetailItem | null {
      const text = getPropertyOrDefaultFromOpaqueObject<string|null>(rawData, "text", null)
      if (text) {
          return new TextItem(text)
      }

      const rawRegion = getPropertyOrDefaultFromOpaqueObject<unknown|null>(rawData, "region", null)
      if (rawRegion) {
          const header = getPropertyOrDefaultFromOpaqueObject<string|null>(rawRegion, "header", null)
          const items = getPropertyOrDefaultFromOpaqueObject<Array<unknown>|null>(rawRegion, "items", null)
          if (header && items) {
              return new RegionItem(header, DetailedResult.parseDetailItemArray(items))
          }
      }

      const rawTree = getPropertyOrDefaultFromOpaqueObject<unknown|null>(rawData, "tree", null)
      if (rawTree) {
          const columnModel = getPropertyOrDefaultFromOpaqueObject<TreeGridColumnModel|null>(rawTree, "columnModel", null)
          const roots = getPropertyOrDefaultFromOpaqueObject<unknown|null>(rawTree, "roots", null)
          if (columnModel && roots) {
              return new TreeGridItem(columnModel, TreeGridRow.parseRows(roots))
          }
      }
      return null
  }
}

/**
 * Represents a text item.
 */
export class TextItem extends DetailItem {
  private _mdText: MarkdownText;

  /**
   * @param {string} text The original text.
   */
  constructor(text: string) {
      super(DetailItemType.Text)

      this._mdText = new MarkdownText(text)
  }

  /**
   * Array of Markdown fragments.
   *
   * @returns {MarkdownText} The Markdown fragments.
   */
  public get text(): MarkdownText {
      return this._mdText
  }

  /**
   * The original text, before it was parsed.
   *
   * @returns {string} The original text.
   */
  public get originalText(): string {
      return this._mdText.originalText
  }
}

/**
 * Represents a region.
 */
export class RegionItem extends DetailItem {
  private _header: string;
  private _items: DetailItem[] = [];

  /**
   * @param {string} header The region's header.
   * @param {DetailItem[]} items The region's containing items.
   */
  constructor(header: string, items: DetailItem[]) {
      super(DetailItemType.Region)
      this._header = header
      this._items = items
  }

  /**
   * The region's header
   *
   * @returns {string} The header.
   */
  public get header() {
      return this._header
  }

  /**
   * Array of containing detail items
   *
   * @returns {DetailItem[]} Containing items.
   */
  public get items(): DetailItem[] {
      return this._items
  }
}

/**
 * Represents a column header in a TreeGrid.
 */
export class TreeGridColumn {
  /** The column's localized name */
  public localizedName = "";
  /** Is the column sortable? */
  public sortable = false;
  /** The default sort direction ("asc", "desc") */
  public defaultSortDirection = "asc";
  /** The column's width ratio (a number between 0 and 1) */
  public widthRatio = 1;
}

/**
 * The view model for a TreeGrid column
 */
export class TreeGridColumnModel {
  /** Array of columns for the tree grid */
  public columns: TreeGridColumn[] = [];
  /** Default sorting column. The id must refers to the position of the column in the columns array */
  public defaultSortedColumnId = 0;
}

/**
 * Represents a row in a TreeGrid
 */
export class TreeGridRow {
  /** Array of cell values for the current row */
  public values: Array< MarkdownText | unknown>= [];
  /** Array of children rows that will apear when the current row is expanded */
  public children: TreeGridRow[] = [];

  // eslint-disable-next-line jsdoc/no-undefined-types
  /**
   * @param {unknown} rawData The opaque TreeGrid row object.
   * @returns {TreeGridRow[]} The parsed rows.
   */
  public static parseRows(rawData: unknown): TreeGridRow[] {
      if (!Array.isArray(rawData)) {
          throw new Error("The row json needs to be an array")
      }
      const rawRows = rawData
      return rawRows.map((row) => {
          const ret = new TreeGridRow()
          const values = getPropertyOrDefaultFromOpaqueObject<unknown[] | null>(row, "values", null)
          if (values) {
              ret.values = values.map((val) => {
                  if (typeof val === "string") {
                      const durationContent = TimeSpan.fromNETTimeSpanStringInvariant(val)
                      if (durationContent.isValid) {
                          return new MarkdownText(durationContent.toString())
                      }

                      return new MarkdownText(val)
                  }
                  return val
              })
          } else {
              throw new Error("The row doesn't contain a values property")
          }
          const children = getPropertyOrDefaultFromOpaqueObject<unknown|null>(row, "children", null)
          ret.children = TreeGridRow.parseRows(children)
          return ret
      })
  }
}

/**
 * Represents a tree grid.
 */
export class TreeGridItem extends DetailItem {
  private _columnModel: TreeGridColumnModel;
  private _roots: TreeGridRow[];

  /**
   * @param {TreeGridColumnModel} columnModel The column view model.
   * @param {TreeGridRow[]} roots The root rows.
   */
  constructor(columnModel: TreeGridColumnModel, roots: TreeGridRow[]) {
      super(DetailItemType.Tree)
      this._columnModel = columnModel
      this._roots = roots
  }

  /** @inheritdoc */
  public getType(): string {
      return "tree"
  }

  /**
   * The column model
   *
   * @returns {TreeGridColumnModel} The column model.
   */
  public get columnModel(): TreeGridColumnModel {
      return this._columnModel
  }

  /**
   * Array of rows that will be initially rendered in the grid
   *
   * @returns {TreeGridRow[]} The roots.
   */
  public get roots(): TreeGridRow[] {
      return this._roots
  }
}

/**
 * Utility function to dereference properties from opaque types
 *
 * @template ExpectedType
 * @param {any} obj The opaque object.
 * @param {string} propertyName The property name to extract.
 * @param {ExpectedType} defaultValue A default value to return if the object doesn't have the required property.
 * @returns {ExpectedType} The property or default value.
 */
// eslint-disable-next-line @typescript-eslint/no-explicit-any
function getPropertyOrDefaultFromOpaqueObject<ExpectedType>(obj: any, propertyName: string, defaultValue: ExpectedType): ExpectedType {
    if (propertyName in obj) {
        // eslint-disable-next-line @typescript-eslint/no-unsafe-member-access
        return obj[propertyName] as ExpectedType
    }
    return defaultValue
}