import type { AnalysisResult } from "./Analysis"
import type { DetailedResult, RegionItem, TextItem } from "./DetailedResult"
import { DetailItemType } from "./DetailedResult"
import { HighlightedResultType, HighlightedResult } from "./HighlightedResult"
import type { MarkdownLinkFragment } from "./MarkdownParser"
import { MarkdownFragmentType, parseMarkdown } from "./MarkdownParser"

// See: https://dev.azure.com/devdiv/DevDiv/_git/Concord?path=/src/ManImpl.NetStandard/Common/ErrorCodes.cs&version=GBmain&_a=contents
enum SyncOverAsyncResultCode {
    Error = "AA0001",
    MainError = "AA0004",
    StarveError = "AA0005",
    StressError = "AA0006",
    ThreadQueueError = "AA0007",
  }

/**
 * Represents a single sync over async finding.
 */
export class SyncOverAsyncFinding {
    #_waitingMethod: MarkdownLinkFragment;
    #_waitingOnMethod: MarkdownLinkFragment;

    /**
     * ctor
     *
     * @param {MarkdownLinkFragment} waitingMethod A markdown fragment that points to the frame that's waiting.
     * @param {MarkdownLinkFragment} waitingOnMethod A markdown fragment that points to the frame being waited on.
     */
    constructor(waitingMethod: MarkdownLinkFragment, waitingOnMethod: MarkdownLinkFragment) {
        this.#_waitingMethod = waitingMethod
        this.#_waitingOnMethod = waitingOnMethod
    }

    /**
     * The method that's waiting.
     *
     * @returns {MarkdownLinkFragment} A link pointing to the method frame that's waiting.
     */
    public get waitingMethod(): MarkdownLinkFragment {
        return this.#_waitingMethod
    }

    /**
     * The method that's being waited on.
     *
     * @returns {MarkdownLinkFragment} A link pointing to the method frame that's being waited on.
     */
    public get waitingOnMethod(): MarkdownLinkFragment {
        return this.#_waitingOnMethod
    }
}

/**
 * Represents a highlighted result for SyncOverAsync results
 */
export class SyncOverAsyncHighlightedResult extends HighlightedResult {
    /**
     * Check if a detailed result is a SyncOverAsync result.
     *
     * @param {DetailedResult} detailedResult The detailed result.
     * @returns {boolean} true/false
     */
    public static isSyncOverAsyncResult(detailedResult: DetailedResult): boolean {
        return (
            detailedResult.errorCode === SyncOverAsyncResultCode.Error ||
            detailedResult.errorCode === SyncOverAsyncResultCode.MainError ||
            detailedResult.errorCode === SyncOverAsyncResultCode.StarveError
        )
    }

    /**
     * ctor
     */
    constructor() {
        super(HighlightedResultType.SyncOverAsync)
    }

    /**
     * @inheritdoc
     */
    public get isValid(): boolean {
        return this.#_findings.length > 0
    }

    /**
     * All SyncOverAsync findings
     *
     * @returns {SyncOverAsyncFinding} An array of findings.
     */
    public get findings(): SyncOverAsyncFinding[] {
        return this.#_findings
    }

    /**
     * Add an analysis result in the highlighted card. If the result is not sync over async the method will return false.
     *
     * @param {AnalysisResult} result The analysis result.
     * @returns {boolean} true if the add operation was successful.
     */
    public tryAddResult(result: AnalysisResult): boolean {
        if (!SyncOverAsyncHighlightedResult.isSyncOverAsyncResult(result.detailedResult) || !result.detailedResult || result.detailedResult.items.length === 0) {
            return false
        }

        // Find the analysis summary region
        const summaryRegions = result.detailedResult.items.filter((r) => {
            return (r.type === DetailItemType.Region && (r as RegionItem).header === "Analysis summary")
        }).map((r) => {
            return r as RegionItem
        })

        if (!summaryRegions || summaryRegions.length !== 1 || summaryRegions[0].items.length === 0 || summaryRegions[0].items[0].type !== DetailItemType.Text) {
            return false
        }

        const summary = (summaryRegions[0].items[0] as TextItem).originalText

        const messageExpr =
      /((Asynchronous method)|([0-9]+ threads are performing asynchronous work in method)) '(?<methodFrameLink>.+)'(( is waiting on a synchronous call to)|(, but are waiting on a synchronous call to method)) '(?<waitingOnFrameLink>.+)'/

        const messageMatch = summary.match(messageExpr)
        if (!messageMatch || !messageMatch.groups) {
            return false
        }

        const linkMd = parseMarkdown(messageMatch.groups.methodFrameLink)
        if (linkMd.length !== 1 || linkMd[0].type !== MarkdownFragmentType.Link) {
            return false
        }
        const waitingMethodLink = linkMd[0] as MarkdownLinkFragment

        const waitingOnMd = parseMarkdown(messageMatch.groups.waitingOnFrameLink)
        if (waitingOnMd.length !== 1 || waitingOnMd[0].type !== MarkdownFragmentType.Link) {
            return false
        }
        const waitingOnMethodLink = waitingOnMd[0] as MarkdownLinkFragment

        this.#_findings.push(new SyncOverAsyncFinding(waitingMethodLink, waitingOnMethodLink))
        this._results.push(result)
        return true
    }

    #_findings: SyncOverAsyncFinding[] = []
}