/**
 * Represents a time duration.
 */
export class TimeSpan {
    /**
     * Create a TimeSpan from an time string formatted with a Constant Specifier.
     *
     * @see https://docs.microsoft.com/dotnet/standard/base-types/standard-timespan-format-strings#the-constant-c-format-specifier
     * @param {string} input The input string in .NET constant format.
     * @returns {TimeSpan} A TimeSpan object.
     */
    public static fromNETTimeSpanStringInvariant(input: string): TimeSpan {
        // format: [-][d.]hh:mm:ss[.fffffff]
        if (!input) {
            return TimeSpan.InvalidTimeSpan
        }
        if (input[0] === "-") { // ignore the negative time interval if present
            input = input.slice(1)
        }
        const components = input.split(":")
        if (components.length !== 3) {
            return TimeSpan.InvalidTimeSpan
        }

        let daysComponent = "0"
        let hoursComponent = components[0]
        const dhComponent = components[0].split(".")
        if (dhComponent.length === 2) {
            daysComponent = dhComponent[0]
            hoursComponent = dhComponent[1]
        }

        const minutesComponent = components[1]

        let secondsComponent = components[2]
        let millisecondsComponent = "0"
        const smComponents = components[2].split(".")
        if (smComponents.length === 2) {
            secondsComponent = smComponents[0]
            millisecondsComponent = smComponents[1]
            if (millisecondsComponent.length !== 7) {
                return TimeSpan.InvalidTimeSpan
            }
            // We don't need a higher resolution; Just use the first 3 fractional digits
            millisecondsComponent = millisecondsComponent.substring(0, 3)
        }
        if (hoursComponent.length !== 2 || minutesComponent.length !== 2 || secondsComponent.length !== 2) {
            return TimeSpan.InvalidTimeSpan
        }

        const days = TimeSpan.parsePositiveIntStrict(daysComponent)
        const hours = TimeSpan.parsePositiveIntStrict(hoursComponent)
        const minutes = TimeSpan.parsePositiveIntStrict(minutesComponent)
        const seconds = TimeSpan.parsePositiveIntStrict(secondsComponent)
        const milliseconds = TimeSpan.parsePositiveIntStrict(millisecondsComponent)
        if (days < 0 || hours < 0 || hours >= 60 || minutes < 0 || minutes >= 60 || seconds < 0 || seconds >= 60 || milliseconds < 0 || milliseconds >= 1000) {
            return TimeSpan.InvalidTimeSpan
        }

        return new TimeSpan(days, hours, minutes, seconds, milliseconds, true)
    }

    /**
     * Create a TimeSpan from a number of milliseconds.
     *
     * @param {number} count Number of milliseconds.
     * @returns {TimeSpan} A TimeSpan object.
     */
    public static fromMilliseconds(count: number): TimeSpan {
        if (count < 0) {
            return TimeSpan.InvalidTimeSpan
        }
        const milliseconds = count % 1000
        count = Math.floor(count / 1000)
        const seconds = count % 60
        count = Math.floor(count / 60)
        const minutes = count % 60
        count = Math.floor(count / 60)
        const hours = count % 60
        count = Math.floor(count / 60)
        const days = count
        return new TimeSpan(days, hours, minutes, seconds, milliseconds)
    }

    /**
     * Parse a string input and expect all chars to be digits.
     *
     * @param {string} input The input.
     * @returns {number} The parsed number or negative number if parsing fails.
     */
    private static parsePositiveIntStrict(input: string): number {
        if (!input) {
            return -1
        }
        for (let i = 0; i < input.length; i++) {
            if (input[i] < "0" || input[i] > "9") {
                return -1
            }
        }

        const ret = parseInt(input)
        if (isNaN(ret)) {
            return -1
        }
        return ret
    }

    /**
     * Returns whether the TimeSpan is invalid.
     *
     * @returns {boolean} Is the TimeSpan valid?
     */
    public get isValid(): boolean {
        return this.#_isValid
    }

    /**
     * Get the days.
     *
     * @returns {number} The days component in the TimeSpan.
     */
    public get days(): number {
        return this.#_days
    }

    /**
     * Get the hours.
     *
     * @returns {number} The hours component in the TimeSpan.
     */
    public get hours(): number {
        return this.#_hours
    }

    /**
     * Get the minutes.
     *
     * @returns {number} The minutes component in the TimeSpan.
     */
    public get minutes(): number {
        return this.#_minutes
    }

    /**
     * Get the seconds.
     *
     * @returns {number} The seconds component in the TimeSpan.
     */
    public get seconds(): number {
        return this.#_seconds
    }

    /**
     * Get the milliseconds.
     *
     * @returns {number} The milliseconds component in the TimeSpan.
     */
    public get milliseconds(): number {
        return this.#_milliseconds
    }

    /**
     * Returns a milliseconds value of this TimeSpan.
     *
     * @returns {number} The milliseconds value.
     */
    public toMilliseconds(): number {
        return (this.#_days * 86400 + this.#_hours * 3600 + this.#_minutes * 60 + this.#_seconds) * 1000 + this.#_milliseconds
    }

    /**
     * Returns a the time duration as a human readable string.
     *
     * @returns {string} The string output.
     */
    public toString(): string {
        if (this.toMilliseconds() === 0) {
            return "0 seconds"
        }

        let retStr = ""
        if (this.#_days > 0) {
            retStr = this.#_days === 1 ? "1 day " : `${this.#_days} days `
        }
        if (this.#_hours > 0) {
            retStr += this.#_hours === 1 ? "1 hour " : `${this.#_hours} hours `
        }
        if (this.#_minutes > 0) {
            retStr += this.#_minutes === 1 ? "1 minute " : `${this.#_minutes} minutes `
        }
        if (this.#_seconds > 0) {
            retStr += this.#_seconds === 1 ? "1 second " : `${this.#_seconds} seconds `
        }
        if (this.#_milliseconds > 0) {
            retStr += this.#_milliseconds === 1 ? "1 millisecond " : `${this.#_milliseconds} milliseconds `
        }
        // trim the trailing space
        return retStr.trimEnd()
    }

    /**
     * ctor
     *
     * @param {number} days The days component.
     * @param {number} hours The hours component.
     * @param {number} minutes The minutes component.
     * @param {number} seconds The seconds component.
     * @param {number} milliseconds The milliseconds component.
     * @param {boolean} isValid Is the TimeSpan valid?
     */
    private constructor(days: number, hours: number, minutes: number, seconds: number, milliseconds: number, isValid: boolean = true) {
        this.#_isValid = isValid
        this.#_days = days
        this.#_hours = hours
        this.#_minutes = minutes
        this.#_seconds = seconds
        this.#_milliseconds = milliseconds
    }

    private static InvalidTimeSpan: TimeSpan = new TimeSpan(-1, -1, -1, -1, -1, false)

    #_isValid: boolean;
    #_days: number
    #_hours: number
    #_minutes: number
    #_seconds: number
    #_milliseconds: number
}