import React from "react";
import "./Asset.less";

interface HeaderProps {
  text: string;
}

export class Header extends React.Component<HeaderProps> {
  constructor(props: any) {
    super(props);
    this.state = {};
  }

  render(): React.ReactNode {
    return (
      <>
        <div className="header">
          <h3 className="title">{this.props.text}</h3>
        </div>
      </>
    );
  }
}
