import type { AnalysisResult } from "./Analysis"

export enum HighlightedResultType {
    SyncOverAsync,
}

/**
 * Base class that defines a generic Highlighted result.
 */
export abstract class HighlightedResult {
    #_type: HighlightedResultType;
    protected _results: AnalysisResult[] = [];

    /**
     * @param {HighlightedResultType} type The HighlightedResult type.
     */
    protected constructor(type: HighlightedResultType) {
        this.#_type = type
    }

    /**
     * The results's type.
     *
     * @returns {HighlightedResultType} The result type.
     */
    public get type(): HighlightedResultType {
        return this.#_type
    }

    /**
     * Is the highlighted card valid?
     */
     public abstract get isValid(): boolean;

     /**
      * All analysis results that are covered by this highlighted result.
      *
      * @returns {AnalysisResult[]} The analysis results.
      */
     public get includedResults(): AnalysisResult[] {
         return this._results
     }
}