import React, { Component } from "react";
import { Link } from "@fluentui/react";
import "./Styles/FooterSection.less";
import { Navigator } from "../Utils/Navigator";
import { NoticeCard } from "../CardContent/NoticeCard";
import { RenderUtils } from "../../RenderUtils";

export interface FooterSectionProps {
  analyzerVersion: string | null;
}

export default class FooterSection extends Component<FooterSectionProps, unknown> {
  constructor(props: FooterSectionProps) {
    super(props);
    Navigator.registerRoute("notices", "", () => this.renderNoticeCard());
  }

  render(): React.ReactNode {
    const viewerVersion = RenderUtils.GetResultsViewerVersion();
    return (
      <div className="footer-section">
        <Link aria-label="Third Party Notices" onClick={this.showNotice}>
          Third Party Notices
        </Link>
        {this.props.analyzerVersion && <span>DiagnosticAnalysis version: {this.props.analyzerVersion}</span>}
        {viewerVersion && <span>Viewer version: {RenderUtils.GetResultsViewerVersion()}</span>}
      </div>
    );
  }

  private renderNoticeCard(): React.ReactNode {
    return <NoticeCard />;
  }

  private showNotice() {
    Navigator.showModal("notices");
  }
}
