import React, { Component } from "react";
import Results from "./components/CardContent/Results";
import { Navigator } from "./components/Utils/Navigator";
import { Analysis } from "ResultsParser/src/Analysis";
import Ajv from "ajv";
import pako from "pako";
import * as schema from "./Schema.json";
import { AssetsContext } from "./components/Utils/Contexts";
import { AzureArtifactInformation, GetArtifactUrl } from "./components/Utils/OpenInVs";

// the results variable is set in the html page,
// and made available for static rendering, without
// the need of a web server. Note, these are declared
// in the html file, so disable linting.
// eslint-disable-next-line no-var,@typescript-eslint/no-explicit-any
declare var compressedAnalysisResults: any;

// this variable is for the uncompressed version of the JSON
// for backwards compatibility.
// eslint-disable-next-line no-var,@typescript-eslint/no-explicit-any
declare var rootAnalysisResults: any;

// the artifact information variable that overrides the "artifactResourceId" & "artifactBlobUri" query parameters
// eslint-disable-next-line no-var
declare var artifactInformation: AzureArtifactInformation;

export interface AppState {
  analysis: Analysis | null;
  isLoading: boolean;
  isJsonValid: boolean;
  artifactUrl: string | null;
}

export default class App extends Component<unknown, AppState> {
  constructor(props: unknown) {
    super(props);
    this.state = { analysis: null, isLoading: true, isJsonValid: true, artifactUrl: null };
  }

  async getResults() {
    try {
      const artifactUrl = GetArtifactUrl(artifactInformation, window.location);

      if (compressedAnalysisResults) {
        const decodedBase64 = window.atob(compressedAnalysisResults);
        const deflatedBuffer = new Uint8Array(new ArrayBuffer(decodedBase64.length));
        for (let i = 0; i < decodedBase64.length; i++) {
          deflatedBuffer[i] = decodedBase64.charCodeAt(i);
        }

        const inflatedBuffer = pako.inflate(deflatedBuffer);
        const decoder = new TextDecoder("utf-8");
        rootAnalysisResults = JSON.parse(decoder.decode(inflatedBuffer));
      } else if (!rootAnalysisResults) {
        const urlParams = new URLSearchParams(window.location.search);
        const inputPath: string = urlParams.get("input") ?? "";
        const response = await fetch(inputPath);
        rootAnalysisResults = await response.json();
      }

      const ajv = new Ajv();
      const validate = ajv.compile(schema);
      const valid = validate(rootAnalysisResults);
      if (!valid) {
        console.log(validate.errors);
        this.setState({ isJsonValid: false, artifactUrl: artifactUrl });
      } else {
        this.setState({ analysis: Analysis.parse(rootAnalysisResults), artifactUrl: artifactUrl });
      }
    } catch (error) {
      console.log(error);
      this.setState({ isJsonValid: false });
    } finally {
      this.setState({ isLoading: false });
    }
  }

  componentDidMount(): void {
    this.getResults();
  }

  render() {
    return !this.state.isLoading ? (
      <div className="container">
        <header className="pageTitle">
          <h1>Diagnostic Analysis</h1>
        </header>
        {this.state.analysis && (
          <AssetsContext.Provider value={this.state.analysis.assets}>
            <Results
              analysis={this.state.analysis}
              isJsonValid={this.state.isJsonValid}
              artifactUrl={this.state.artifactUrl}
            />
            <Navigator />
          </AssetsContext.Provider>
        )}
      </div>
    ) : (
      <div></div>
    );
  }
}
