import React, { Component } from "react";
import { MemoryDumpCollectionTrigger, MemoryDumpSummary } from "ResultsParser/src/MemoryDumpSummary";
import KeyValueList, { KeyValueEntry } from "../Utils/KeyValueList";
import "./Styles/ArtifactSummarySection.less";
import "./Styles/FrontPageCard.less";
import { ResultCard } from "../CardContent/ResultCard";
import { ArtifactSummary, ArtifactSummaryType } from "ResultsParser/src/ArtifactSummary";
import { TraceSummary } from "ResultsParser/src/TraceSummary";

export interface ArtifactSummaryProps {
  summary: ArtifactSummary;
}

export default class ArtifactSummarySection extends Component<ArtifactSummaryProps, unknown> {
  render() {
    let memoryDumpSummary: MemoryDumpSummary | null = null;
    let traceSummary: TraceSummary | null = null;

    const appServiceEntries: KeyValueEntry[] = [];
    const overviewEntries: KeyValueEntry[] = [];

    if (this.props.summary.artifactSummaryType === ArtifactSummaryType.MemoryDump) {
      memoryDumpSummary = this.props.summary as MemoryDumpSummary;

      overviewEntries.push({
        key: "Process Name:",
        value: `${memoryDumpSummary.processPath} ${memoryDumpSummary.processArgs}`,
      });

      if (memoryDumpSummary.processArchitecture) {
        overviewEntries.push({ key: "Process Architecture:", value: memoryDumpSummary.processArchitecture });
      }

      overviewEntries.push({
        key: "Heap Information:",
        value: memoryDumpSummary.hasHeapInformation ? "Present" : "Missing",
      });

      if (memoryDumpSummary.clrVersion) {
        overviewEntries.push({ key: "CLR Version:", value: memoryDumpSummary.clrVersion });
      }
      if (memoryDumpSummary.cpuCount > 0) {
        overviewEntries.push({ key: "CPU Count:", value: memoryDumpSummary.cpuCount.toString() });
      }
      const processUptime = memoryDumpSummary.processUptime;
      if (processUptime.isValid && processUptime.toMilliseconds() > 0) {
        overviewEntries.push({ key: "Process uptime:", value: processUptime.toString() });
      }

      const collectionTriggerStr = ArtifactSummarySection.getUserTextForCollectionTrigger(
        memoryDumpSummary.collectionTrigger,
        memoryDumpSummary.isAzureAppService
      );

      if (memoryDumpSummary.isAzureAppService) {
        appServiceEntries.push(
          { key: "Site Name:", value: memoryDumpSummary.appServiceSiteName },
          { key: "Machine Name:", value: memoryDumpSummary.appServiceMachineName },
          { key: "Platform Version:", value: memoryDumpSummary.appServicePlatformVersion }
        );

        if (collectionTriggerStr) {
          appServiceEntries.push({ key: "Collection reason:", value: collectionTriggerStr });
        }
      } else if (collectionTriggerStr) {
        overviewEntries.push({ key: "Collection reason:", value: collectionTriggerStr });
      }
    } else {
      traceSummary = this.props.summary as TraceSummary;

      overviewEntries.push({ key: "Trace File", value: traceSummary.fileName });

      if (traceSummary.machineName) {
        overviewEntries.push({ key: "Machine Name", value: traceSummary.machineName });
      }

      if (traceSummary.cpuCount > 0) {
        overviewEntries.push({ key: "CPU Count:", value: traceSummary.cpuCount.toString() });
      }

      overviewEntries.push(
        { key: "Process Name:", value: traceSummary.processName },
        { key: "Process Id:", value: traceSummary.processId.toString() },
        { key: "Process Bitness:", value: traceSummary.processBitness }
      );

      const sessionDuration = traceSummary.sessionDuration;
      if (sessionDuration.isValid && sessionDuration.toMilliseconds() > 0) {
        overviewEntries.push(
          { key: "Session Start:", value: traceSummary.sessionStart },
          { key: "Session Duration:", value: sessionDuration.toString() }
        );
      }

      overviewEntries.push({
        key: "CallStack Information:",
        value: traceSummary.hasCallStacks ? "Present" : "Missing",
      });
    }

    const body: React.ReactNode = (
      <>
        <KeyValueList
          headerText="Process Overview"
          entries={overviewEntries}
          isInitiallyCollapsed={false}
          rowClassName="summary-entry"
          keyClassName="entry-header"
          valueClassName="entry-value"
        />

        {appServiceEntries.length > 0 && (
          <KeyValueList
            headerText="App Service"
            entries={appServiceEntries}
            isInitiallyCollapsed={false}
            rowClassName="summary-entry"
            keyClassName="entry-header"
            valueClassName="entry-value"
          />
        )}
      </>
    );

    return (
      <ResultCard
        className="artifact-summary-section front-page-card"
        headerContent={<></>}
        bodyContent={body}
      ></ResultCard>
    );
  }

  private static getUserTextForCollectionTrigger(
    trigger: MemoryDumpCollectionTrigger,
    wasCollectedOnAzureAppService: boolean
  ): string | null {
    switch (trigger) {
      case MemoryDumpCollectionTrigger.AutoHealTotalRequests:
        return wasCollectedOnAzureAppService
          ? "Diagnose and Solve Auto-Heal's request count limit was reached"
          : "The request count limit was reached";
      case MemoryDumpCollectionTrigger.AutoHealMemory:
        return wasCollectedOnAzureAppService
          ? "Diagnose and Solve Auto-Heal's memory limit was reached"
          : "The memory limit was reached";
      case MemoryDumpCollectionTrigger.AutoHealStatusCode:
        return wasCollectedOnAzureAppService
          ? "Diagnose and Solve Auto-Heal detected a HTTP Server error"
          : "Some responses failed";
      case MemoryDumpCollectionTrigger.AutoHealSlowRequests:
        return wasCollectedOnAzureAppService
          ? "Diagnose and Solve Auto-Heal detected slow requests"
          : "Some requests were slow";
      case MemoryDumpCollectionTrigger.AutoHealPercentMemory:
        return wasCollectedOnAzureAppService
          ? "Diagnose and Solve Auto-Heal's memory limit was reached"
          : "The memory limit was reached";
      case MemoryDumpCollectionTrigger.AutoHealPercentSlowRequests:
        return wasCollectedOnAzureAppService
          ? "Diagnose and Solve Auto-Heal detected slow requests"
          : "Some requests were slow";
      case MemoryDumpCollectionTrigger.AutoHealStartupFailures:
        return wasCollectedOnAzureAppService
          ? "Diagnose and Solve Auto-Heal detected the application failed to start"
          : "The application failed to start";
      default:
        return null;
    }
  }
}
