import { TooltipHost, ITooltipProps, ITooltipHostStyles, Link, Panel, PanelType } from "@fluentui/react";
import React, { useContext } from "react";
import { MarkdownFragment, MarkdownFragmentType, MarkdownLinkFragment } from "ResultsParser/src/MarkdownParser";
import { Navigator } from "./Navigator";
import "./MarkdownFragment.less";
import { AssetFactory } from "../Assets/AssetFactory";
import { AssetsContext } from "../Utils/Contexts";
import { AssetsCollection } from "ResultsParser/src/Assets";

interface MarkdownFragmentProps {
  id: string;
  fragment: MarkdownFragment;
}

interface MarkdownFragmentState {
  fragment: MarkdownFragment;
  className: string;
  link: URL | undefined;
  linkType: LinkType;
  assetPanelOpen: boolean;
}

function ParseSourceUrl(searchParams: URLSearchParams): URL | undefined {
  const useVSCode = false;

  const sourceProvider = searchParams.get("source-code-provider");
  const sourceUrl = searchParams.get("source-code-url");
  if (!sourceUrl || !sourceProvider) {
    return;
  }

  const decodedUrl = decodeURIComponent(sourceUrl);
  const parsedUrl = new URL(decodedUrl);

  if (!useVSCode) {
    return parsedUrl;
  }

  // Use vscode web for supported providers
  if (sourceProvider === "GitHub") {
    parsedUrl.hostname = "github.dev";
  } else if (sourceProvider === "AzureRepos" || sourceProvider === "AzureDevOpsServer") {
    // Azure Repos integration is currently in Preview and as a limitation
    // currently only supports read-only mode.
    // ref: https://code.visualstudio.com/docs/editor/vscode-web#_current-limitations
    const originalHostName = parsedUrl.hostname;
    parsedUrl.hostname = "vscode.dev";
    parsedUrl.pathname = "/" + originalHostName + parsedUrl.pathname;
  }

  return parsedUrl;
}

function isParameterTrue(searchParams: URLSearchParams, key: string): boolean {
  return searchParams.get(key)?.toLowerCase() === "true";
}

enum LinkType {
  None,
  External,
  Modal,
  Asset,
  MissingSymbols,
  MissingSourceLink,
}

export class MarkdownFragmentComponent extends React.Component<MarkdownFragmentProps, MarkdownFragmentState> {
  static contextType = AssetsContext;

  constructor(props: MarkdownFragmentProps) {
    super(props);

    let className = "TextContent";
    let link = undefined;
    let linkType = LinkType.None;

    if (props.fragment.type == MarkdownFragmentType.Link) {
      const markdownLink = props.fragment as MarkdownLinkFragment;
      const urlSearchParams = new URLSearchParams(markdownLink.link.search);

      link = markdownLink.link;
      switch (markdownLink.link.protocol) {
        case "thread:":
        case "il:": {
          if (markdownLink.link.protocol === "thread:") {
            className += " ThreadReference";
            linkType = LinkType.Asset;
          } else {
            className += " SourceReference";

            const newLink = ParseSourceUrl(urlSearchParams);
            if (newLink) {
              link = newLink;
              linkType = LinkType.External;
            } else if (isParameterTrue(urlSearchParams, "is-user-code")) {
              if (isParameterTrue(urlSearchParams, "has-symbols")) {
                linkType = LinkType.MissingSourceLink;
                className += " MissingSourceLink";
              } else {
                linkType = LinkType.MissingSymbols;
                className += " MissingSymbols";
              }
            }
          }

          break;
        }
        case "clrobject:":
          className += " ClrObjectReference";
          break;
        case "clrmodule:":
          className += " ClrModuleReference";
          linkType = LinkType.Asset;
          break;
        case "info:":
          className = "";
          linkType = LinkType.Modal;
          break;
        case "https:":
          className = "";
          linkType = LinkType.External;
          break;
        default:
          break;
      }
    }
    this.state = {
      fragment: props.fragment,
      className: className,
      link: link,
      linkType: linkType,
      assetPanelOpen: false,
    };
  }

  private renderMissingSourceLinkInfoLink(): React.ReactNode {
    const tooltipId = `${this.props.id}_tooltip`;

    const content =
      "If symbols with Source Link enabled are provided, this will become a link to the specific line of your source code.";

    const modalErrorCode = this.state.linkType === LinkType.MissingSymbols ? "AA0028" : "AA0029";
    const showModalFunc = () => {
      Navigator.showFirstModalForErrorCode(modalErrorCode);
    };

    const hostStyles: Partial<ITooltipHostStyles> = {
      root: { display: "inline-block" },
    };

    const tooltipProps: ITooltipProps = {
      onRenderContent: () => (
        <p>
          {content}
          <br />
          <Link aria-label="Learn more." onClick={showModalFunc}>
            Learn more.
          </Link>
        </p>
      ),
    };

    return (
      <TooltipHost tooltipProps={tooltipProps} id={tooltipId} styles={hostStyles}>
        <span className={this.state.className} aria-describedby={tooltipId}>
          {this.state.fragment.value}
        </span>
      </TooltipHost>
    );
  }

  private renderAssetLink(assets: AssetsCollection): React.ReactNode {
    const linkFragment = this.state.fragment as MarkdownLinkFragment;
    if (this.state.link == undefined) {
      throw "no link for asset?";
    }

    const [displayName, assetComponent] = AssetFactory.createAssetComponent(
      `asset_${this.props.id}`,
      this.state.link,
      assets
    );
    if (assetComponent === undefined) {
      return this.renderUnknownLink();
    }

    return (
      <>
        <Link onClick={() => this.setState({ assetPanelOpen: true })}>{linkFragment.value}</Link>
        <Panel
          isLightDismiss
          isOpen={this.state.assetPanelOpen}
          onDismiss={() => this.setState({ assetPanelOpen: !this.state.assetPanelOpen })}
          type={PanelType.medium}
          closeButtonAriaLabel="Close"
          headerText={displayName}
          className="asset-panel"
        >
          {assetComponent}
        </Panel>
      </>
    );
  }

  private renderModalLink(): React.ReactNode {
    const linkFragment = this.state.fragment as MarkdownLinkFragment;
    return (
      <Link
        onClick={() => {
          alert("Modal for " + this.state.link?.pathname);
        }}
      >
        {linkFragment.value}
      </Link>
    );
  }

  private renderExternalLink(): React.ReactNode {
    const linkFragment = this.state.fragment as MarkdownLinkFragment;
    return (
      <Link href={this.state.link?.toString()} target="_blank">
        {linkFragment.value}
      </Link>
    );
  }

  private renderUnknownLink(): React.ReactNode {
    return <span className={this.state.className}>{this.state.fragment.value}</span>;
  }

  render(): React.ReactNode {
    const assets: AssetsCollection = this.context;

    switch (this.state.linkType) {
      case LinkType.Modal:
        return this.renderModalLink();
      case LinkType.External:
        return this.renderExternalLink();
      case LinkType.Asset:
        return this.renderAssetLink(assets);
      case LinkType.MissingSourceLink:
      case LinkType.MissingSymbols:
        return this.renderMissingSourceLinkInfoLink();
      default:
        return this.renderUnknownLink();
    }
  }
}
