import { DetailsList, DetailsListLayoutMode, IColumn, SelectionMode } from "@fluentui/react";
import React from "react";
import { TreeGridColumn, TreeGridItem, TreeGridRow } from "ResultsParser/src/DetailedResult";
import { MarkdownFragment, MarkdownText } from "ResultsParser/src/MarkdownParser";
import { MarkdownFragmentComponent } from "./MarkdownFragmentComponent";
import { RowExpander } from "./RowExpander";

class TreeGridListRow {
  public static asRootRow(row: TreeGridRow, id: string) {
    return new TreeGridListRow(row, id, 0);
  }

  public getContent(colIdx: number): MarkdownText | unknown {
    return this._row.values[colIdx];
  }

  public get id(): string {
    return this._id;
  }

  public get indentation(): number {
    return this._indentation;
  }

  public get hasChildren(): boolean {
    return this._row.children.length > 0;
  }

  public getChildren(): TreeGridListRow[] {
    return this._children;
  }

  public enumerate(): TreeGridListRow[] {
    // pre-order tree traversal, stop at non expanded nodes
    let ret: TreeGridListRow[] = [this];
    if (this.hasChildren && this.isExpanded) {
      ret = ret.concat(
        this._children
          .map((c) => {
            return c.enumerate();
          })
          .reduce((a, b) => {
            return a.concat(b);
          })
      );
    }
    return ret;
  }

  public isExpanded: boolean;

  private constructor(row: TreeGridRow, id: string, indentation: number) {
    this._row = row;
    this.isExpanded = false;
    this._id = id;
    this._indentation = indentation;
    this._children = this._row.children.map((row, index) => {
      return new TreeGridListRow(row, this._id + "_" + index, this.indentation + 1);
    });
  }

  private _row: TreeGridRow;
  private _children: TreeGridListRow[];
  private _id: string;
  private _indentation: number;
}

class TreeGridListRoots {
  constructor(roots: TreeGridRow[]) {
    this._roots = roots.map((row, index) => {
      return TreeGridListRow.asRootRow(row, index.toString());
    });
  }

  public sort(descending: boolean, columnIndex: number) {
    this._roots.sort((first: TreeGridListRow, second: TreeGridListRow) => {
      const x: MarkdownText | unknown = first.getContent(columnIndex);
      const y: MarkdownText | unknown = second.getContent(columnIndex);

      // sort as string, unless both values are numbers
      const n1 = Number(x);
      const n2 = Number(y);
      if (!isNaN(n1) && !isNaN(n2)) {
        if (descending) {
          return n1 < n2 ? 1 : -1;
        } else {
          return n1 < n2 ? -1 : 1;
        }
      }
      const compareResult = (x as MarkdownText).originalText
        .toLowerCase()
        .localeCompare((y as MarkdownText).originalText);
      if (compareResult === 0) {
        return 0;
      }
      if (descending) {
        return compareResult > 0 ? -1 : 1;
      } else {
        return compareResult > 0 ? 1 : -1;
      }
    });
  }

  public enumerate(): TreeGridListRow[] {
    return this._roots
      .map((root) => {
        return root.enumerate();
      })
      .reduce((a, b) => {
        return a.concat(b);
      });
  }

  private _roots: TreeGridListRow[];
}

interface ITreeGridListColumn extends IColumn {
  sortable: boolean;
  columnIndex: number;
}

interface TreeGridProps {
  id: string;
  treeGridItem: TreeGridItem;
}
interface TreeGridState {
  visibleItems: TreeGridListRow[];
  columns: ITreeGridListColumn[];
}

export class TreeGridList extends React.Component<TreeGridProps, TreeGridState> {
  private treeRoots: TreeGridListRoots;

  constructor(props: TreeGridProps) {
    super(props);

    this.treeRoots = new TreeGridListRoots(props.treeGridItem.roots);
    const columns = props.treeGridItem.columnModel.columns.map((col: TreeGridColumn, index) => {
      const sortedDescending = col.defaultSortDirection == "desc";
      const column: ITreeGridListColumn = {
        key: props.id + "_col_" + index,
        columnIndex: index,
        name: col.localizedName,
        fieldName: col.localizedName,
        minWidth: 50,
        currentWidth: col.widthRatio * 100,
        flexGrow: (col.widthRatio * 100) | 0,
        isResizable: true,
        data: this,
        sortable: col.sortable,
        showSortIconWhenUnsorted: col.sortable,
        isSorted: col.sortable && props.treeGridItem.columnModel.defaultSortedColumnId == index,
        isSortedDescending: col.sortable ? sortedDescending : undefined,
        onColumnClick: this._onColumnClick,
      };

      return column;
    });

    if (columns[props.treeGridItem.columnModel.defaultSortedColumnId].sortable) {
      this.treeRoots.sort(
        Boolean(columns[props.treeGridItem.columnModel.defaultSortedColumnId].isSortedDescending),
        props.treeGridItem.columnModel.defaultSortedColumnId
      );
    }

    this.state = {
      visibleItems: this.treeRoots.enumerate(),
      columns: columns,
    };
  }

  private expandCollapseRow(row: TreeGridListRow, shouldExpand: boolean) {
    if (!row.hasChildren || (shouldExpand && row.isExpanded) || (!shouldExpand && !row.isExpanded)) {
      return;
    }

    row.isExpanded = shouldExpand;
    this.setState({
      visibleItems: this.treeRoots.enumerate(),
    });
  }

  private renderItemColumn(
    item?: TreeGridListRow,
    rowIndex?: number | undefined,
    c?: IColumn | undefined
  ): JSX.Element {
    if (!c) {
      return null as unknown as JSX.Element;
    }
    const thisObj = c.data as TreeGridList;
    if (!thisObj) {
      return null as unknown as JSX.Element;
    }
    if (!item) {
      return null as unknown as JSX.Element;
    }

    const column = c as ITreeGridListColumn;

    const shouldIncludeIndentation = column.columnIndex === 0 && item.indentation > 0;
    const width = item.indentation * 50 + "px";
    const shouldRenderExpandCollapseButton = column.columnIndex === 0 && item.hasChildren;

    let fragmentNodes: React.ReactNode = null;
    const content = item.getContent(column.columnIndex);
    if (content instanceof MarkdownText) {
      const mdText = content as MarkdownText;
      fragmentNodes = mdText.fragments.map((fragment: MarkdownFragment, index: number) => {
        const id = `${thisObj.props.id}_md_${item.id}_${index}`;
        return <MarkdownFragmentComponent key={id} id={id} fragment={fragment} />;
      });
    } else if (Number(content)) {
      fragmentNodes = Number(content).toLocaleString();
    } else {
      fragmentNodes = content;
    }

    return (
      <span>
        {shouldIncludeIndentation && (
          <span style={{ display: "inline-block", width }} role="presentation">
            &nbsp;
          </span>
        )}
        {shouldRenderExpandCollapseButton && (
          <RowExpander
            isExpanded={item.isExpanded}
            data={thisObj}
            onExpand={(t) => {
              (t as TreeGridList).expandCollapseRow(item, true);
            }}
            onCollapse={(t) => {
              (t as TreeGridList).expandCollapseRow(item, false);
            }}
          />
        )}
        <span>{fragmentNodes}</span>
      </span>
    );
  }

  render() {
    return (
      <DetailsList
        className="DetailItem_Table"
        items={this.state.visibleItems}
        columns={this.state.columns}
        selectionMode={SelectionMode.none}
        onRenderItemColumn={this.renderItemColumn}
        layoutMode={DetailsListLayoutMode.fixedColumns}
        onItemInvoked={this.onItemInvoked}
      />
    );
  }

  private onItemInvoked = (item: TreeGridListRow) => {
    if (item.hasChildren) {
      this.expandCollapseRow(item, !item.isExpanded);
    }
  };

  private _onColumnClick = (ev: React.MouseEvent<HTMLElement>, column: IColumn): void => {
    const newColumns: ITreeGridListColumn[] = this.state.columns.slice();
    const currColumn: ITreeGridListColumn = newColumns.filter((currCol) => column.key === currCol.key)[0];

    if (currColumn.sortable) {
      newColumns.forEach((newCol: IColumn) => {
        if (newCol === currColumn) {
          currColumn.isSortedDescending = !currColumn.isSortedDescending;
          currColumn.isSorted = true;
        } else {
          newCol.isSorted = false;
          newCol.isSortedDescending = true;
        }
      });

      this.treeRoots.sort(Boolean(currColumn.isSortedDescending), currColumn.columnIndex);
      this.setState({
        visibleItems: this.treeRoots.enumerate(),
        columns: newColumns,
      });
    }
  };
}
