import React, { Component } from "react";
import "./Styles/ErrorSection.less";
import "./Styles/FrontPageCard.less";
import { ResultCard } from "../CardContent/ResultCard";

export enum ErrorType {
  JsonError,
}

interface IErrorSectionProps {
  types: ErrorType[];
}

interface IErrorSectionState {
  errors: string[];
}

export default class ErrorSection extends Component<IErrorSectionProps, IErrorSectionState> {
  constructor(props: IErrorSectionProps) {
    super(props);
    this.state = {
      errors: [],
    };
  }
  render() {
    this.props.types.forEach((type) => {
      if (type == ErrorType.JsonError) {
        this.state.errors.push(
          "Errors have been detected while parsing the results. " +
            "This is caused by reading from a corrupted or invalid json result file which may come from manual or unintended modifications. " +
            "Some content may be broken."
        );
      }
    });

    const body: React.ReactNode = this.state.errors.map((error, index) => {
      return (
        <>
          {index != 0 && <br />}
          <div>{error}</div>
        </>
      );
    });
    return (
      <ResultCard
        className="error-section front-page-card"
        headerContent={<h2>Errors detected while parsing Diagnostic Analysis Results</h2>}
        bodyContent={body}
      ></ResultCard>
    );
  }
}
