import React, { Component } from "react";
import { SeverityIcon } from "../Utils/SeverityIcon";
import { MarkdownFragmentComponent } from "../Utils/MarkdownFragmentComponent";
import { SyncOverAsyncHighlightedResult } from "ResultsParser/src/SyncOverAsyncHighlightedResult";
import SurveyPrompt from "../Utils/SurveyPrompt";

import "./Styles/SyncOverAsyncSection.less";
import "./Styles/FrontPageCard.less";
import { ResultCard } from "../CardContent/ResultCard";

export interface SyncOverAsyncCardProps {
  result: SyncOverAsyncHighlightedResult;
}

export default class SyncOverAsyncSection extends Component<SyncOverAsyncCardProps, unknown> {
  render() {
    if (!this.props.result.isValid) {
      return <section />;
    }

    const allErrorCodes = this.props.result.includedResults.map((f) => f.detailedResult.errorCode);

    const finding = this.props.result.findings[0];
    const otherOccurrences = this.props.result.findings.slice(1).map((f, index) => {
      const methodFragmentId = `syncOverAsync_method_${index}`;
      const waitingOnFragmentId = `syncOverAsync_waitingOn_${index}`;

      return {
        methodFragment: (
          <MarkdownFragmentComponent key={methodFragmentId} id={methodFragmentId} fragment={f.waitingMethod} />
        ),
        waitingOnFragment: (
          <MarkdownFragmentComponent key={waitingOnFragmentId} id={waitingOnFragmentId} fragment={f.waitingOnMethod} />
        ),
      };
    });

    const primaryMethodFragmentId = `syncOverAsync_primary_method`;
    const primaryWaitingOnFragmentId = `syncOverAsync_primary_waitingOn`;

    const header: React.ReactNode = (
      <h2 tabIndex={-1}>
        <SeverityIcon level="error" /> Sync over Async
      </h2>
    );

    const body: React.ReactNode = (
      <>
        <div>
          <MarkdownFragmentComponent
            key={primaryMethodFragmentId}
            id={primaryMethodFragmentId}
            fragment={finding.waitingMethod}
          />{" "}
          is synchronously waiting on{" "}
          <MarkdownFragmentComponent
            key={primaryWaitingOnFragmentId}
            id={primaryWaitingOnFragmentId}
            fragment={finding.waitingOnMethod}
          />
          , causing thread pool starvation. Your app may experience the following symptoms under load:
          <ul>
            <li>Slow web requests</li>
            <li>Loss of throughput</li>
            <li>Request timeouts</li>
          </ul>
        </div>

        {otherOccurrences.length > 0 && (
          <div>
            There are {otherOccurrences.length} more occurrence(s) of this problem:
            <ul>
              {otherOccurrences.map((o, i) => (
                <li key={i}>
                  <code>{o.methodFragment}</code> is waiting on <code>{o.waitingOnFragment}</code>
                </li>
              ))}
            </ul>
          </div>
        )}

        <h3>Example</h3>
        <pre
          dangerouslySetInnerHTML={{
            __html: `<span class="comment">// BAD: Calling Wait() or Result can cause thread pool starvation</span>
<span class="keyword">public int</span> <span class="identifier">SyncOverAsyncAntiPattern</span>() 
{
    <span class="identifier">CallDependencyAsync</span>().<span class="error">Wait()</span>;
    <span class="keyword">var</span> <span class="identifier">result</span> = <span class="identifier">ReadDataAsync</span>().<span class="error">Result</span>;
    <span class="keyword">return</span> <span class="identifier">result</span> + <span class="literal">1</span>;
}

<span class="comment">// GOOD: Using the await keyword throughout the call chain</span>
<span class="keyword">public async Task</span>&lt;<span class="keyword">int</span>&gt; <span class="identifier">DoSomethingAsync</span>() 
{
    <span class="keyword">await</span> <span class="identifier">CallDependencyAsync</span>();
    <span class="keyword">var</span> <span class="identifier">result</span> = <span class="keyword">await</span> <span class="identifier">ReadDataAsync</span>();
    <span class="keyword">return</span> <span class="identifier">result</span> + <span class="literal">1</span>;
}`,
          }}
        />

        <h3>Why is this bad?</h3>
        <p>
          Once you go async, all of your callers should be async. In many cases, being partially async can be worse than
          being entirely synchronous.
        </p>
        <a
          href="https://github.com/davidfowl/AspNetCoreDiagnosticScenarios/blob/master/AsyncGuidance.md/#avoid-using-taskresult-and-taskwait"
          target="_blank"
        >
          Learn More: Avoid using Task.Result and Task.Wait
        </a>

        <h3>How do I fix it?</h3>
        <p>
          Use the <code>await</code> keyword to get the result from calling an async method.
        </p>
        <a
          href="https://github.com/davidfowl/AspNetCoreDiagnosticScenarios/blob/master/AsyncGuidance.md/#asynchrony-is-viral"
          target="_blank"
        >
          Learn More: Asynchrony is viral
        </a>

        <SurveyPrompt survey="general" errorCodes={allErrorCodes} />
      </>
    );

    return (
      <ResultCard
        className={"syncAsync-section front-page-card"}
        headerContent={header}
        bodyContent={body}
        isCollapsible={true}
      ></ResultCard>
    );
  }
}
