import React from "react";
import { TimeSpan } from "ResultsParser/src/TimeSpan";
import { ThreadAsset } from "../../AnalysisResults";
import KeyValueList from "../Utils/KeyValueList";
import "./Asset.less";
import { Header } from "./Header";
import { StackTrace } from "./StackTrace";

export interface ThreadProps {
  id: string;
  threadObj: ThreadAsset;
  assetLink: URL;
}

interface ThreadState {
  frameBaseAddressToFocusOn: string | undefined;
}

export class Thread extends React.Component<ThreadProps, ThreadState> {
  public static displayName(assetObj: ThreadAsset): string {
    return `Thread: [${assetObj.id}] ${assetObj.name}`;
  }

  constructor(props: ThreadProps) {
    super(props);

    const urlSearchParams = new URLSearchParams(props.assetLink.search);
    const frameBase = urlSearchParams.get("frame-base") ?? undefined;

    this.state = {
      frameBaseAddressToFocusOn: frameBase,
    };
  }

  render(): React.ReactNode {
    const entries = [
      { key: "Name:", value: this.props.threadObj.name },
      { key: "Id:", value: this.props.threadObj.id.toString() },
      { key: "State:", value: this.props.threadObj.state },
    ];

    if (this.props.threadObj.createTime !== undefined) {
      const createDate = new Date(Date.parse(this.props.threadObj.createTime));
      entries.push({ key: "Create date:", value: createDate.toLocaleString() });
    }

    if (this.props.threadObj.totalTime !== undefined) {
      const cpuTime = TimeSpan.fromNETTimeSpanStringInvariant(this.props.threadObj.totalTime);
      entries.push({ key: "Total CPU time:", value: cpuTime.toString() });
    }

    return (
      <>
        <Header text="Properties" />
        <KeyValueList
          headerText=""
          entries={entries}
          isInitiallyCollapsed={false}
          rowClassName="keyvaluelist-entry"
          keyClassName="keyvaluelist-key"
          valueClassName="keyvaluelist-value"
        />
        <Header text="Call stack" />
        {this.props.threadObj.stack && (
          <StackTrace
            id={`stacktrace_${this.props.id}`}
            defaultJMC={true}
            stackTrace={this.props.threadObj.stack}
            frameBaseAddressToFocusOn={this.state.frameBaseAddressToFocusOn}
          />
        )}
      </>
    );
  }
}
