import React from "react";
import { Link } from "@fluentui/react";
import { ModalWindow } from "./Navigator";
import "./OpenInVs.less";

export interface AzureArtifactInformation {
  resourceId: string; // /subscriptions/<subscription id>/resourceGroups/<resource group>/providers/Microsoft.Storage/storageAccounts/<storage account name>
  blobUri: string; // Example: https://<storage account name>.blob.core.windows.net/<container name>/<path to blob>
}

export function GetArtifactUrl(
  artifactInformation: AzureArtifactInformation | null,
  windowLocation: Location
): string | null {
  let artifact: AzureArtifactInformation = {
    resourceId: "",
    blobUri: "",
  };

  if (artifactInformation) {
    artifact = artifactInformation;
  } else {
    const urlParams = new URLSearchParams(windowLocation.search);
    artifact.resourceId = urlParams.get("artifactResourceId") ?? "";
    artifact.blobUri = urlParams.get("artifactBlobUri") ?? "";
  }

  if (!artifact.resourceId || !artifact.blobUri) {
    return null;
  }

  const blobExpr = /https:\/\/[a-z0-9.]+\/(?<blobPath>.+)/;
  const blobMatch = artifact.blobUri.match(blobExpr);
  if (!blobMatch || !blobMatch.groups) {
    return null;
  }

  // https://dev.azure.com/devdiv/DevDiv/_wiki/wikis/DevDiv.wiki/29676/Visual-Studio-remote-diagnostic-requests
  return `vsweb+diag://debug/dmp?source=azstorage&resource=${artifact.resourceId}&path=${blobMatch.groups.blobPath}&defaultViews=analysis`;
}

export interface OpenInVsProps {
  artifactUrl: string;
}

export const OpenInVs: React.FunctionComponent<OpenInVsProps> = (props: OpenInVsProps) => {
  const [showRequestIssuedWindow, setShowRequestIssuedWindow] = React.useState<boolean>(false);

  const toggleRequestIssuedWindow = () => {
    setShowRequestIssuedWindow(!showRequestIssuedWindow);
  };

  if (!props.artifactUrl) {
    return <></>;
  }

  return (
    <>
      <div className="openInVsContainer">
        <Link id="openInVsLink" href={props.artifactUrl} onClick={toggleRequestIssuedWindow}>
          Open in Visual Studio (2022 17.4 Preview 2)
        </Link>
        {showRequestIssuedWindow && (
          <ModalWindow onClose={toggleRequestIssuedWindow}>
            <div className={"openInVSNotification"}>
              <h2>A request to open Visual Studio was issued.</h2>
              <br />
              If Visual Studio failed to open it probably means you don't have the right version installed.
              <br />
              <b>Visual Studio 2022 17.4 Preview 2</b> or higher is required. Don't have Visual Studio?{" "}
              <Link href={"https://visualstudio.microsoft.com/downloads/"} target={"_blank"}>
                Get it now.
              </Link>
            </div>
          </ModalWindow>
        )}
      </div>
    </>
  );
};
