import type { RawResult } from "./Analysis"

export enum ArtifactSummaryType {
    None,
    MemoryDump,
    Trace
}

/**
 * Represents an artifact summary
 */
export abstract class ArtifactSummary {
    /**
     * If the raw result is a summary for an artifact, get the type of artifact.
     *
     * @param {RawResult} result The raw result
     * @returns {ArtifactSummaryType} The type of artifact
     */
    public static getArtifactSummaryType(result: RawResult): ArtifactSummaryType {
        switch (result.ErrorCode) {
            case "AA0030":
                return ArtifactSummaryType.MemoryDump
            case "TA0001":
                return ArtifactSummaryType.Trace
            default:
                return ArtifactSummaryType.None
        }
    }

    /**
     * @returns {RawResult} The underlying raw result
     */
    public abstract get result(): RawResult

    /**
     * @returns {ArtifactSummaryType} The type of artifact
     */
    public abstract get artifactSummaryType(): ArtifactSummaryType
}