import React from "react";
import { ClrModuleAsset } from "../../AnalysisResults";
import KeyValueList from "../Utils/KeyValueList";
import "./Asset.less";
import { Header } from "./Header";

export interface ClrModuleProps {
  id: string;
  moduleObj: ClrModuleAsset;
}

export class ClrModule extends React.Component<ClrModuleProps> {
  public static displayName(assetObj: ClrModuleAsset): string {
    return `Module: ${assetObj.name}`;
  }

  constructor(props: ClrModuleProps) {
    super(props);
    this.state = {};
  }

  render(): React.ReactNode {
    const entries = [
      { key: "Name:", value: this.props.moduleObj.name },
      { key: "Version:", value: this.props.moduleObj.version },
      { key: "Found symbols:", value: this.props.moduleObj.hasSymbols ? "Yes" : "No" },
      { key: "File path:", value: this.props.moduleObj.fullName },
    ];

    return (
      <>
        <Header text="Properties" />
        <KeyValueList
          headerText=""
          entries={entries}
          isInitiallyCollapsed={false}
          rowClassName="keyvaluelist-entry"
          keyClassName="keyvaluelist-key"
          valueClassName="keyvaluelist-value"
        />
      </>
    );
  }
}
