import { IconButton } from "@fluentui/react";
import React from "react";
import "./RowExpander.less";

interface RowExpanderProps {
  isExpanded: boolean;
  data: unknown;
  onExpand: (data: unknown) => void;
  onCollapse: (data: unknown) => void;
}

export class RowExpander extends React.Component<RowExpanderProps, unknown> {
  constructor(props: RowExpanderProps) {
    super(props);
  }

  private toggle() {
    const data = this.props.data;

    if (!this.props.isExpanded) {
      this.props.onExpand(data);
    } else {
      this.props.onCollapse(data);
    }
  }

  render() {
    if (!this.props.isExpanded) {
      return (
        <IconButton
          iconProps={{ iconName: "ChevronRightMed" }}
          title="Expand"
          ariaLabel="Expand"
          role="none"
          className="rowExpander-expand-icon"
          onClick={() => {
            this.toggle();
          }}
        />
      );
    } else {
      return (
        <IconButton
          iconProps={{ iconName: "ChevronRightMed" }}
          title="Collapse"
          ariaLabel="Collapse"
          role="none"
          className="rowExpander-collapse-icon"
          onClick={() => {
            this.toggle();
          }}
        />
      );
    }
  }
}
